import React from "react";
import { Link, useHistory } from "react-router-dom";

export default function DatatableUserTypeList({ data }) {
  const history = useHistory();
  return (
    <div className="table-responsive">
      <table className="table table-hover institute-table">
        <thead>
          <tr className="inst-list-heading">
            <th className="inst-list-th text-center">SL No.</th>
            <th className="inst-list-th text-center">User Type</th>
            <th className="inst-list-th text-center" colSpan="2">
              Action
            </th>
          </tr>
        </thead>
        <tbody>
          {data.map((row, index) => (
            <tr key={index}>
              <td className="inst-list-td text-center">{index + 1}</td>
              <td className="inst-list-td text-center"> {row["typename"]}</td>
              <td
                className="inst-list-td text-center action"
                title="View Details"
                onClick={() =>
                  history.push({
                    pathname: "/view-user-type",
                    state: { id: row["id"] },
                  })
                }
              >
                <i className="far fa-eye action-icon"></i>
              </td>
              <td
                className="inst-list-td text-center action"
                title="Edit"
                onClick={() =>
                  history.push({
                    pathname: "/edit-user-type",
                    state: { id: row["id"] },
                  })
                }
              >
                <i className="far fa-edit action-icon"></i>
              </td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
}
