import React, { useState, useEffect } from "react";
import { useHistory } from "react-router-dom";
import { useForm } from "react-hook-form";
import axios from "axios";
import { trackPromise } from "react-promise-tracker";
import { LoadingIndicator } from "../Loading/LoadingIndicator";
import styled from "styled-components";
import { toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import "../../App.css";
const ErrorMsg = styled.p`
  color: red;
  font-size: 14px;
  font-family: "Source Sans Pro", sans-serif;
  font-weight: bold;
`;

export default function CreateBillForm() {
  const { register, handleSubmit, errors } = useForm(); // initialise the hook

  let history = useHistory();
  const onSubmit = (data) => {
    const userEmail = localStorage.getItem("emailAddress");
    data["generated_by"] = userEmail;
    //console.log(data);

    let accessToken = localStorage.getItem("token");
    //alert(accessToken);
    accessToken = accessToken.substring(1, accessToken.length - 1);

    var config = {
      method: "post",
      url: "/CreateNewInvoice",
      headers: {
        Authorization: `Bearer ${accessToken}`,
      },
      data: data,
    };

    trackPromise(
      axios(config).then(function (response) {
        // console.log(JSON.stringify(response.data));
        if (response.data.status === "ok") {
          toast("Bill Created Successfully", {
            className: "toast-error",
          });
          history.push("/billing-list-view");
        } else {
          toast(response.data.msg, {
            className: "toast-error",
          });
        }
      })
    );
  };

  const [instituteName, setInstituteName] = useState("");
  const [billingAmount, setBillingAmount] = useState("");
  /*INSTITUTE ID DROPDOWN START */
  const [instituteIdData, setInstituteIdData] = useState([]);
  useEffect(() => {
    async function fetchData() {
      let accessToken = localStorage.getItem("token");
      // alert(accessToken);
      accessToken = accessToken.substring(1, accessToken.length - 1);
      var config = {
        method: "get",
        url: "/GetAllInstituteIDs",
        headers: {
          Authorization: `Bearer ${accessToken}`,
        },
      };

      const instIdApiResp = await trackPromise(axios(config));
      // console.log(
      //   "instIdApiResp - " + JSON.stringify(instIdApiResp.data.msg.data)
      // );
      setInstituteIdData(instIdApiResp.data.msg.data);
    }
    fetchData();
  }, []);

  //onchange institute id dropdown menu, institute name value will update -start
  const ChangeInstName = (e) => {
    //setUser({ ...user, division: e.target.value });
    var index = e.target.selectedIndex;
    var optionElement = e.target.childNodes[index];
    var id = optionElement.getAttribute("data-id");
    //console.log("id: " + id);

    async function fetchData() {
      let accessToken = localStorage.getItem("token");
      // alert(accessToken);
      accessToken = accessToken.substring(1, accessToken.length - 1);
      var config = {
        method: "post",
        url: "/institute-detail",
        headers: {
          Authorization: `Bearer ${accessToken}`,
        },
        data: { institute_id: id },
      };

      const instDetailApiResp = await trackPromise(axios(config));
      // console.log(
      //   "instDetailApiResp - " + JSON.stringify(instDetailApiResp.data.msg)
      // );
      setInstituteName(instDetailApiResp.data.msg.name);
      setBillingAmount(instDetailApiResp.data.msg.payment_amount);
    }
    fetchData();
  };
  //onchange institute id dropdown menu, institute name value will update -end
  /*INSTITUTE ID DROPDOWN END */
  return (
    <div>
      {/* create form general info start */}
      <form className="mt-4" onSubmit={handleSubmit(onSubmit)}>
        {/* institute id */}
        <div className="form-group row">
          <div className="col-sm-12 col-md-6 col-lg-6 col-xl-6">
            <label>
              <span className="red-star">*</span>Institute ID
            </label>
            <br />
            {instituteIdData.length === 0 ? null : (
              <select
                className="form-control"
                id="institute_id"
                name="institute_id"
                defaultValue="--Select Institute ID--"
                onChange={(e) => ChangeInstName(e)}
                ref={register({ required: true })}
              >
                <option defaultValue hidden>
                  --Select Institute ID--
                </option>
                {/* FOR SORTING ASCENDING ORDER ->  .sort((a, b) => a.name.localeCompare(b.name)) */}
                {instituteIdData
                  .sort((a, b) => a.institute_id.localeCompare(b.institute_id))
                  .map((e, key) => {
                    return (
                      <option key={key} data-id={e.id} value={e.institute_id}>
                        {e.institute_id}
                      </option>
                    );
                  })}
              </select>
            )}
            {errors.institute_id && (
              <ErrorMsg>* This Field is Required</ErrorMsg>
            )}
          </div>
        </div>
        {/* institute name */}
        <div className="form-group row">
          <div className="col-sm-12 col-md-6 col-lg-6 col-xl-6">
            <label>
              <span className="red-star">*</span>Institute Name
            </label>
            <br />
            <input
              type="text"
              readOnly
              className="form-control"
              name="institute_name"
              defaultValue={instituteName}
              ref={register({ required: true })}
            />
            {errors.institute_name && (
              <ErrorMsg>* This Field is Required</ErrorMsg>
            )}
          </div>
        </div>
        {/* billing month */}
        <div className="form-group row">
          <div className="col-sm-12 col-md-6 col-lg-6 col-xl-6">
            <label>
              <span className="red-star">*</span>Billing Month
            </label>
            <br />
            <select
              className="form-control"
              name="month"
              ref={register({ required: true })}
            >
              <option hidden defaultValue>
                ---Select---
              </option>
              <option value="January">January</option>
              <option value="February">February</option>
              <option value="March">March</option>
              <option value="April">April</option>
              <option value="May">May</option>
              <option value="June">June</option>
              <option value="July">July</option>
              <option value="August">August</option>
              <option value="September">September</option>
              <option value="October">October</option>
              <option value="November">November</option>
              <option value="December">December</option>
            </select>
            {errors.month && <ErrorMsg>* This Field is Required</ErrorMsg>}
          </div>
        </div>
        {/* billing year */}
        <div className="form-group row">
          <div className="col-sm-12 col-md-6 col-lg-6 col-xl-6">
            <label>
              <span className="red-star">*</span>Billing Year
            </label>
            <br />
            <select
              className="form-control"
              name="year"
              ref={register({ required: true })}
            >
              <option hidden defaultValue>
                ---Select---
              </option>
              <option value="2020">2020</option>
              <option value="2021">2021</option>
              <option value="2022">2022</option>
              <option value="2023">2023</option>
              <option value="2024">2024</option>
              <option value="2025">2025</option>
              <option value="2026">2026</option>
              <option value="2027">2027</option>
              <option value="2028">2028</option>
              <option value="2029">2029</option>
              <option value="2030">2030</option>
              <option value="2031">2031</option>
              <option value="2032">2032</option>
              <option value="2033">2033</option>
              <option value="2034">2034</option>
              <option value="2035">2035</option>
              <option value="2036">2036</option>
              <option value="2037">2037</option>
              <option value="2038">2038</option>
              <option value="2039">2039</option>
              <option value="2040">2040</option>
              <option value="2041">2041</option>
              <option value="2042">2042</option>
              <option value="2043">2043</option>
              <option value="2044">2044</option>
              <option value="2045">2045</option>
              <option value="2046">2046</option>
              <option value="2047">2047</option>
              <option value="2048">2048</option>
              <option value="2049">2049</option>
              <option value="2050">2050</option>
            </select>
            {errors.year && <ErrorMsg>* This Field is Required</ErrorMsg>}
          </div>
        </div>
        {/* Billing Amount */}
        <div className="form-group row">
          <div className="col-sm-12 col-md-6 col-lg-6 col-xl-6">
            <label>
              <span className="red-star">*</span>Billing Amount
            </label>
            <br />
            <input
              type="number"
              className="form-control"
              name="amount"
              readOnly
              defaultValue={billingAmount}
              ref={register({
                required: "* This Field is Required",
                pattern: {
                  value: /^[+]?\d+([.]\d+)?$/,
                  message: "* Please Enter Positive Number",
                },
              })}
            />
            {errors.amount && <ErrorMsg>{errors.amount.message}</ErrorMsg>}
          </div>
        </div>

        <button type="submit" className="btn btn-update">
          Bill Generate
        </button>
      </form>
      {/* create form general info end */}
      <LoadingIndicator />
    </div>
  );
}
