import React, { useState, useEffect } from "react";
import { Link, useHistory } from "react-router-dom";
import axios from "axios";
import { trackPromise } from "react-promise-tracker";
import { toast } from "react-toastify";
import Moment from "moment";
export default function DatatableBillingList({ data }) {
  const history = useHistory();

  return (
    <div className="table-responsive">
      <table className="table table-hover institute-table">
        <thead>
          <tr className="inst-list-heading">
            <th className="inst-list-th text-center">SL No.</th>
            <th className="inst-list-th text-center">Invoice Number</th>
            <th className="inst-list-th text-center">Institute ID</th>
            <th className="inst-list-th text-center">Institute Name</th>
            <th className="inst-list-th text-center">Billing Month</th>
            <th className="inst-list-th text-center">Billing Amount</th>
            <th className="inst-list-th text-center">Bill Generate Date</th>
            <th className="inst-list-th text-center" colSpan="3">
              Action
            </th>
          </tr>
        </thead>
        <tbody>
          {data.map((row, index) => (
            <tr key={index}>
              <td className="inst-list-td text-center">{index + 1}</td>
              <td className="inst-list-td text-center">{row["id"]}</td>
              <td className="inst-list-td text-center">
                {row["institute_id"]}
              </td>
              <td className="inst-list-td text-center">
                {row["institute_name"]}
              </td>
              <td className="inst-list-td text-center">
                {row["month"]}-{row["year"]}
              </td>
              <td className="inst-list-td text-center">{row["amount"]}</td>
              <td className="inst-list-td text-center">{row["created_at"]}</td>
              <td
                className="inst-list-td text-center action"
                title="View Details"
                onClick={() =>
                  history.push({
                    pathname: "/billing-invoice-view",
                    state: { id: row["id"] },
                  })
                }
              >
                <i className="far fa-eye action-icon"></i>
              </td>
              <td
                className="inst-list-td text-center action"
                title="Edit"
                onClick={() =>
                  history.push({
                    pathname: "/update-bill",
                    state: { id: row["id"] },
                  })
                }
              >
                <i className="far fa-edit action-icon"></i>
              </td>
              <td className="inst-list-td text-center action" title="Send Bill">
                <Link to="#">
                  <i className="far fa-share-square  action-icon"></i>
                </Link>
              </td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
}
