import React, { useState, useEffect } from "react";
import { Link, useHistory } from "react-router-dom";
import axios from "axios";
import { trackPromise } from "react-promise-tracker";
import { toast } from "react-toastify";
import Moment from "moment";
export default function DatatableInstList({ data }) {
  const history = useHistory();
  /*TOGGLE INSTITUTE STATUS START*/
  const [isActive, setisActive] = useState(false);
  let status;
  function toggleInstStatus(id) {
    setisActive(isActive ? false : true);
    isActive ? (status = 1) : (status = 0);
    // console.log("STATUS: " + id);
    postData(id, status);
    async function postData(id, status) {
      let accessToken = localStorage.getItem("token");
      //alert(accessToken);
      accessToken = accessToken.substring(1, accessToken.length - 1);
      var config = {
        method: "post",
        url: "/institute-status-update",
        headers: {
          Authorization: `Bearer ${accessToken}`,
          "Content-Type": "application/json",
        },
        data: {
          id: id,
          active: status,
        },
      };
      const instStatusApiResp = await trackPromise(axios(config));
      //console.log("instStatusApiResp - " + instStatusApiResp.data);
      if (instStatusApiResp.data.status === "ok") {
        toast(`Institute Status ${status ? "Active" : "Inactive"}`, {
          className: "toast-error",
        });
      } else {
        toast(instStatusApiResp.data.msg, {
          className: "toast-error",
        });
      }
    }
  }
  /*TOGGLE INSTITUTE STATUS END*/

  return (
    <div className="table-responsive">
      <table className="table table-hover institute-table">
        <thead>
          <tr className="inst-list-heading">
            <th className="inst-list-th text-center">SL No.</th>
            <th className="inst-list-th text-center">Institute ID</th>
            <th className="inst-list-th text-center">Institute Name</th>
            <th className="inst-list-th text-center">Email</th>
            <th className="inst-list-th text-center">Phone / Mobile No.</th>
            <th className="inst-list-th text-center">Package Name</th>
            <th className="inst-list-th text-center">Subscription End</th>
            <th className="inst-list-th text-center">Status</th>
            <th className="inst-list-th text-center" colSpan="3">
              Action
            </th>
          </tr>
        </thead>
        <tbody>
          {data.map((row, index) => (
            <tr key={index}>
              <td className="inst-list-td text-center">{index + 1}</td>
              <td className="inst-list-td text-center">
                {row["institute_id"]}
              </td>
              <td className="inst-list-td text-center"> {row["name"]}</td>
              <td className="inst-list-td text-center"> {row["email"]}</td>
              <td className="inst-list-td text-center"> {row["phone"]}</td>
              <td className="inst-list-td text-center"> </td>
              <td className="inst-list-td text-center">
                {Moment.utc(row["subscription_e_date"]).format("DD-MM-YYYY")}
              </td>
              <td className="inst-list-td text-center">
                {row["active"] === "1" ? "Active" : "Inactive"}
              </td>
              <td
                className="inst-list-td text-center action"
                title="View Details"
                onClick={() => history.push(`/institute-detail/${row["id"]}`)}
              >
                <i className="far fa-eye action-icon"></i>
              </td>
              <td
                className="inst-list-td text-center action"
                title="Edit"
                onClick={() =>
                  history.push(
                    `/institute-update/${row["id"]}/${row["package_id"]}`
                  )
                }
              >
                <i className="far fa-edit action-icon"></i>
              </td>
              <td className="inst-list-td text-center action" title="Status">
                {row["id"] ? (
                  <i
                    onClick={() => toggleInstStatus(row["id"])}
                    className={`fas ${
                      isActive ? "fa-toggle-off" : "fa-toggle-on"
                    } action-icon`}
                  ></i>
                ) : null}
              </td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
}
