import PaginationComponent from "../Pagination/PaginationComponent";
import React, { useState, useEffect, useMemo } from "react";
import { Link, useHistory } from "react-router-dom";
import axios from "axios";
import { trackPromise } from "react-promise-tracker";
export default function DatatableBillingHistoryList() {
  const history = useHistory();
  const [data, setData] = useState([]);
  //GET ALL BILLING LIST -START
  useEffect(() => {
    async function fetchData() {
      let accessToken = localStorage.getItem("token");
      // alert(accessToken);
      accessToken = accessToken.substring(1, accessToken.length - 1);
      var config = {
        method: "get",
        url: "/GetAllInvoiceList",
        headers: {
          Authorization: `Bearer ${accessToken}`,
        },
      };

      const billingListApiResp = await trackPromise(axios(config));
      // console.log(
      //   "billingListApiResp - " +
      //     JSON.stringify(billingListApiResp.data.msg.data)
      // );
      /*BILLING STATUS FILTER BY INVOICE STATUS == PENDING  START*/
      let jsObjects = billingListApiResp.data.msg.data;
      let result = jsObjects.filter((obj) => {
        return obj.status === "Approved";
      });
      //console.log(result);
      /*BILLING STATUS FILTER BY INVOICE STATUS == PENDING  END*/

      setData(result);
    }
    fetchData();
  }, []);
  //GET ALL BILLING LIST -END

  /*SEARCH AND PAGINATION CODE START*/
  const [search, setSearch] = useState("");
  const [totalItems, setTotalItems] = useState(0);
  const [currentPage, setCurrentPage] = useState(1);
  const ITEMS_PER_PAGE = 20;

  const currentData = useMemo(() => {
    let computedData = data; //data = response data

    /*SEARCH CODE START */
    if (search) {
      computedData = computedData.filter(
        (d) =>
          d.id.toLowerCase().includes(search.toLowerCase()) ||
          d.institute_id.toLowerCase().includes(search.toLowerCase()) ||
          d.institute_name.toLowerCase().includes(search.toLowerCase()) ||
          d.month.toLowerCase().includes(search.toLowerCase()) ||
          d.year.toLowerCase().includes(search.toLowerCase()) ||
          d.amount.toLowerCase().includes(search.toLowerCase())
      );
    }
    /*SEARCH CODE END */

    setTotalItems(computedData.length);
    return computedData.slice(
      (currentPage - 1) * ITEMS_PER_PAGE,
      (currentPage - 1) * ITEMS_PER_PAGE + ITEMS_PER_PAGE
    );
  }, [data, currentPage, search]);
  //console.log("totalItems " + totalItems);
  /*SEARCH AND PAGINATION CODE END*/
  return (
    <div className="table-responsive">
      <table className="table table-hover institute-table">
        <thead>
          <tr className="inst-list-heading">
            <th className="inst-list-th text-center">SL No.</th>
            <th className="inst-list-th text-center">Invoice Number</th>
            <th className="inst-list-th text-center">Institute ID</th>
            <th className="inst-list-th text-center">Institute Name</th>
            <th className="inst-list-th text-center">Billing Month</th>
            <th className="inst-list-th text-center">Billing Amount</th>
            <th className="inst-list-th text-center">Action</th>
          </tr>
        </thead>
        <tbody>
          {currentData.map((row, index) => (
            <tr key={index}>
              <td className="inst-list-td text-center">{index + 1}</td>
              <td className="inst-list-td text-center">{row["id"]}</td>
              <td className="inst-list-td text-center">
                {row["institute_id"]}
              </td>
              <td className="inst-list-td text-center">
                {row["institute_name"]}
              </td>
              <td className="inst-list-td text-center">
                {row["month"]}-{row["year"]}
              </td>
              <td className="inst-list-td text-center">{row["amount"]}</td>
              <td
                className="inst-list-td text-center action"
                title="View Details"
                onClick={() =>
                  history.push({
                    pathname: "/billing-invoice-view",
                    state: { id: row["id"] },
                  })
                }
              >
                <i className="far fa-eye action-icon"></i>
              </td>
            </tr>
          ))}
        </tbody>
      </table>
      <PaginationComponent
        total={totalItems}
        itemsPerPage={ITEMS_PER_PAGE}
        currentPage={currentPage}
        onPageChange={(page) => setCurrentPage(page)}
      />
    </div>
  );
}
