import React, { useState, useEffect } from "react";
import { Link, useHistory } from "react-router-dom";
import axios from "axios";
import { trackPromise } from "react-promise-tracker";
import { toast } from "react-toastify";

export default function DatatableInstType() {
  const [data, setData] = useState([]);

  const [updateApi, setUpdateApi] = useState(false);
  //GET USERTYPE LIST -START
  useEffect(() => {
    async function fetchData() {
      let accessToken = localStorage.getItem("token");
      // alert(accessToken);
      accessToken = accessToken.substring(1, accessToken.length - 1);
      var config = {
        method: "get",
        url: "/GetSettingsInstituteList",
        headers: {
          Authorization: `Bearer ${accessToken}`,
        },
      };

      const userTypeApiResp = await trackPromise(axios(config));
      // console.log(
      //   "userTypeApiResp - " + JSON.stringify(userTypeApiResp.data.data)
      // );
      setData(userTypeApiResp.data.data);
      setUpdateApi(false);
    }
    fetchData();
  }, [updateApi]);
  //GET USERTYPE LIST -END

  const history = useHistory();

  /*TOGGLE INSTITUTE STATUS START*/
  function togglePackageStatus(id, status) {
    // console.log("ID: " + id + "Status: " + status);

    postData(id, status);
    async function postData(id, status) {
      let accessToken = localStorage.getItem("token");
      //alert(accessToken);
      accessToken = accessToken.substring(1, accessToken.length - 1);
      var config = {
        method: "post",
        url: "/UpdateSettingsInstituteActiveStatus",
        headers: {
          Authorization: `Bearer ${accessToken}`,
          "Content-Type": "application/json",
        },
        data: {
          id: id,
          active: status === "1" ? "0" : "1",
        },
      };
      const instStatusApiResp = await trackPromise(axios(config));
      //console.log("instStatusApiResp - " + instStatusApiResp.data);
      if (instStatusApiResp.data.status === "ok") {
        toast(`Status ${status === "1" ? "Disable" : "Enable"} Successfully`, {
          className: "toast-error",
        });
        setUpdateApi(true);
      } else {
        toast(instStatusApiResp.data.msg, {
          className: "toast-error",
        });
      }
    }
  }
  /*TOGGLE INSTITUTE STATUS END*/

  return (
    <div className="table-responsive">
      <table className="table table-hover institute-table">
        <thead>
          <tr className="inst-list-heading">
            <th className="inst-list-th text-center">SL No.</th>
            <th className="inst-list-th text-center">Type Name</th>
            <th className="inst-list-th text-center">Short Form</th>
            <th className="inst-list-th text-center">Status</th>
            <th className="inst-list-th text-center" colSpan="2">
              Action
            </th>
          </tr>
        </thead>
        <tbody>
          {data.map((row, index) => (
            <tr key={index}>
              <td className="inst-list-td text-center">{index + 1}</td>
              <td className="inst-list-td text-center">
                {row["institute_name"]}
              </td>
              <td className="inst-list-td text-center"> {row["short_form"]}</td>
              <td className="inst-list-td text-center">
                {row["active"] === "1" ? "Enable" : "Disable"}
              </td>
              <td className="inst-list-td text-center action" title="Status">
                <i
                  onClick={() => togglePackageStatus(row["id"], row["active"])}
                  className={`fas ${
                    row["active"] === "1" ? "fa-toggle-on" : "fa-toggle-off"
                  } action-icon`}
                  style={{ fontSize: "20px" }}
                ></i>
              </td>
              <td
                className="inst-list-td text-center action"
                title="Edit"
                onClick={() =>
                  history.push({
                    pathname: "/edit-new-inst-type",
                    state: { id: row["id"] },
                  })
                }
              >
                <i className="far fa-edit action-icon"></i>
              </td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
}
