import React, { useState, useEffect } from "react";
import { useHistory } from "react-router-dom";
import { useForm } from "react-hook-form";
import axios from "axios";
import { trackPromise } from "react-promise-tracker";
import { LoadingIndicator } from "../Loading/LoadingIndicator";
import styled from "styled-components";
import { toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import "../../App.css";
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
const ErrorMsg = styled.p`
  color: red;
  font-size: 14px;
  font-family: "Source Sans Pro", sans-serif;
  font-weight: bold;
`;

export default function CreateUserForm() {
  const { register, handleSubmit, errors } = useForm(); // initialise the hook

  let accessToken = localStorage.getItem("token");
  //alert(accessToken);
  accessToken = accessToken.substring(1, accessToken.length - 1);

  let history = useHistory();
  const onSubmit = (data) => {
    if (selectedDate) {
      //console.log(data);
      data = {
        ...data,
        date_of_birth: selectedDate,
      };

      var config = {
        method: "post",
        url: "/RegisterNewUserNormal",
        headers: {
          Authorization: `Bearer ${accessToken}`,
        },
        data: data,
      };

      trackPromise(
        axios(config).then(function (response) {
          // console.log(JSON.stringify(response.data));
          if (response.data.status === "ok") {
            toast("User Created Successfully", {
              className: "toast-error",
            });
            history.push("/user-list-view");
          } else {
            toast(response.data.msg, {
              className: "toast-error",
            });
          }
        })
      );
    } else {
      setErrors_date_of_birth(true);
    }
  };

  const [data, setData] = useState([]);
  const [role, setRole] = useState("");

  //GET USERTYPE -START
  useEffect(() => {
    async function fetchData() {
      let accessToken = localStorage.getItem("token");
      // alert(accessToken);
      accessToken = accessToken.substring(1, accessToken.length - 1);
      var config = {
        method: "get",
        url: "/GetUserTypeList",
        headers: {
          Authorization: `Bearer ${accessToken}`,
        },
      };

      const userTypeApiResp = await trackPromise(axios(config));
      // console.log(
      //   "userTypeApiResp - " + JSON.stringify(userTypeApiResp.data.data)
      // );
      setData(userTypeApiResp.data.data);
      setRole(userTypeApiResp.data.data.typename);
    }
    fetchData();
  }, []);
  //GET USERTYPE -END

  /*SHOW OR HIDE PASSWORD START*/
  const [passwordShown, setPasswordShown] = useState(false);
  const togglePasswordVisiblity = () => {
    setPasswordShown(passwordShown ? false : true);
  };
  /*SHOW OR HIDE PASSWORD END*/
  const [selectedDate, setSelectedDate] = useState("");
  const [errors_date_of_birth, setErrors_date_of_birth] = useState(false);
  return (
    <div>
      {/* create form general info start */}
      <form className="mt-4" onSubmit={handleSubmit(onSubmit)}>
        {/* firstname and lastname */}
        <div className="form-group row">
          <div className="col-sm-12 col-md-6 col-lg-6 col-xl-6">
            <label>
              <span className="red-star">*</span> First Name
            </label>
            <br />
            <input
              type="text"
              className="form-control"
              name="fname"
              ref={register({
                required: "* This Field is Required",
                pattern: {
                  value: /^[a-z\s]{0,255}$/i,
                  message: "* Please Enter Valid Name",
                },
              })}
            />
            {errors.fname && <ErrorMsg>{errors.fname.message}</ErrorMsg>}
          </div>
          <div className="col-sm-12 col-md-6 col-lg-6 col-xl-6">
            <label>Last Name</label>
            <br />
            <input
              type="text"
              className="form-control"
              name="lname"
              ref={register({
                pattern: {
                  value: /^[a-z\s]{0,255}$/i,
                  message: "* Please Enter Valid Name",
                },
              })}
            />
            {errors.lname && <ErrorMsg>{errors.lname.message}</ErrorMsg>}
          </div>
        </div>
        {/* email and mobile no */}
        <div className="form-group row">
          <div className="col-sm-12 col-md-6 col-lg-6 col-xl-6">
            <label>
              <span className="red-star">*</span>Email
            </label>
            <br />
            <input
              type="email"
              className="form-control"
              name="email"
              ref={register({
                required: "* This Field is Required",
              })}
            />
            {errors.email && <ErrorMsg>{errors.email.message}</ErrorMsg>}
          </div>

          <div className="col-sm-12 col-md-6 col-lg-6 col-xl-6">
            <label>
              <span className="red-star">*</span>Mobile No.
            </label>
            <br />
            <input
              type="text"
              className="form-control"
              name="mobile"
              ref={register({
                required: "* This Field is Required",
                pattern: {
                  value: /^\+[1-9]{1}[0-9]{3,14}$/,
                  message: "* Please Enter Valid Number",
                },
              })}
            />
            {errors.mobile && <ErrorMsg>{errors.mobile.message}</ErrorMsg>}
          </div>
        </div>
        {/* gender and date of birth */}
        <div className="form-group row">
          <div className="col-sm-12 col-md-6 col-lg-6 col-xl-6">
            <label>
              <span className="red-star">*</span>Gender
            </label>
            <br />
            <select
              className="form-control"
              name="gender"
              ref={register({ required: true })}
            >
              <option hidden defaultValue>
                ---Select---
              </option>
              <option value="Male">Male</option>
              <option value="Female">Female</option>
              <option value="Other">Other</option>
            </select>
            {errors.gender && <ErrorMsg>* This Field is Required</ErrorMsg>}
          </div>

          <div className="col-sm-12 col-md-6 col-lg-6 col-xl-6">
            <label>
              <span className="red-star">*</span>Date of Birth
            </label>
            <br />
            <DatePicker
              className="form-control"
              selected={selectedDate}
              onChange={(date) => {
                setSelectedDate(date);
                setErrors_date_of_birth(false);
              }}
              dateFormat="dd/MM/yyyy"
              maxDate={new Date()}
              showYearDropdown
              scrollableMonthYearDropdown
            />
            {errors_date_of_birth && (
              <ErrorMsg>* This Field is Required</ErrorMsg>
            )}
          </div>
        </div>

        {/* username and password */}
        <div className="form-group row">
          <div className="col-sm-12 col-md-6 col-lg-6 col-xl-6">
            <label>
              <span className="red-star">*</span>User Name
            </label>
            <br />
            <input
              type="text"
              className="form-control"
              name="username"
              ref={register({ required: true })}
            />
            {errors.username && <ErrorMsg>* This Field is Required</ErrorMsg>}
          </div>

          <div className="col-sm-12 col-md-6 col-lg-6 col-xl-6">
            <label>
              <span className="red-star">*</span>Password
            </label>
            <br />
            <input
              id="password-field1"
              type={passwordShown ? "text" : "password"}
              className="form-control"
              name="password"
              ref={register({
                required: "* This Field is Required",
              })}
            />

            <span
              onClick={togglePasswordVisiblity}
              toggle="#password-field1"
              className={`fa fa-fw ${
                passwordShown ? "fa-eye-slash" : "fa-eye"
              } field-icon toggle-password`}
            />
            {errors.password && <ErrorMsg>{errors.password.message}</ErrorMsg>}
          </div>
        </div>

        {/* User Type  */}
        <div className="form-group row">
          <div className="col">
            <label>
              <span className="red-star">*</span>User Type
            </label>
            <br />
            {data.length === 0 ? null : (
              <select
                className="form-control"
                id="role"
                name="role"
                value={role}
                onChange={(e) => setRole(e.target.value)}
                ref={register({ required: true })}
              >
                <option defaultValue hidden>
                  --Select User Type--
                </option>
                {/* FOR SORTING ASCENDING ORDER ->  .sort((a, b) => a.name.localeCompare(b.name)) */}
                {data
                  .sort((a, b) => a.typename.localeCompare(b.typename))
                  .map((e, key) => {
                    return (
                      <option key={key} data-id={e.id} value={e.typename}>
                        {e.typename}
                      </option>
                    );
                  })}
              </select>
            )}
            {errors.typename && <ErrorMsg>* This Field is Required</ErrorMsg>}
          </div>
        </div>

        <button type="submit" className="btn btn-update">
          Create
        </button>
      </form>
      {/* create form general info end */}
      <LoadingIndicator />
    </div>
  );
}
