import React, { useState, useEffect } from "react";
import { useHistory } from "react-router-dom";
import { useLocation } from "react-router";
import { useForm } from "react-hook-form";
import axios from "axios";
import { trackPromise } from "react-promise-tracker";
import { LoadingIndicator } from "../Loading/LoadingIndicator";
import styled from "styled-components";
import { toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import "../../App.css";
import DatePicker from "react-datepicker";
const ErrorMsg = styled.p`
  color: red;
  font-size: 14px;
  font-family: "Source Sans Pro", sans-serif;
  font-weight: bold;
`;

export default function UpdateUserProfile(props) {
  let accessToken = localStorage.getItem("token");
  //alert(accessToken);
  accessToken = accessToken.substring(1, accessToken.length - 1);

  const { register, handleSubmit, errors } = useForm(); // initialise the hook

  let history = useHistory();

  //console.log(location);
  const onSubmit = (data) => {
    if (selectedDate) {
      //console.log("selectedDate ... " + selectedDate);

      data = {
        ...data,
        date_of_birth: selectedDate,
        email: props.email,
      };
      var config = {
        method: "post",
        url: "/NormalUserProfileInfoUpdate",
        headers: {
          Authorization: `Bearer ${accessToken}`,
        },
        data: data,
      };
      //console.log("data ... " + JSON.stringify(data));
      trackPromise(
        axios(config).then(function (response) {
          // console.log(JSON.stringify(response.data));
          if (response.data.status === "ok") {
            toast(response.data.msg, {
              className: "toast-error",
            });
            history.push({
              pathname: "/user-detail-view",
              state: { email: props.email },
            });
          } else {
            toast(response.data.msg, {
              className: "toast-error",
            });
          }
        })
      );
    } else {
      setErrors_date_of_birth(true);
    }
  };

  //**************************************************************************** */
  const [user, setUser] = useState({});
  //GetNormalUserProfileInfo
  useEffect(() => {
    async function fetchData() {
      let accessToken = localStorage.getItem("token");
      // alert(accessToken);
      accessToken = accessToken.substring(1, accessToken.length - 1);
      var config = {
        method: "post",
        url: "/GetNormalUserProfileInfo",
        headers: {
          Authorization: `Bearer ${accessToken}`,
        },
        data: {
          email: props.email,
        },
      };

      // test
      let response = await trackPromise(axios(config));
      if (response.data.status === "ok") {
        //console.log("Date: " + response.data.msg.date_of_birth);
        if (response.data.msg.date_of_birth) {
          setSelectedDate(new Date(response.data.msg.date_of_birth));
        }

        setSelectedGender(response.data.msg.gender);
        setSelectedMaritalStatus(response.data.msg.marital_status);
        setSelectedReligion(response.data.msg.religion);
        setUser(response.data.msg);
      }
    }
    fetchData();
  }, []);

  const [selectedDate, setSelectedDate] = useState("");
  const [selectedGender, setSelectedGender] = useState("");
  const [selectedMaritalStatus, setSelectedMaritalStatus] = useState("");
  const [selectedReligion, setSelectedReligion] = useState("");
  const [errors_date_of_birth, setErrors_date_of_birth] = useState(false);
  return (
    <>
      {/* edit form general info start */}
      <form className="mt-4" onSubmit={handleSubmit(onSubmit)}>
        {/* First Name */}
        <div className="form-group row">
          <div className="col">
            <label>
              <span className="red-star">*</span> First Name
            </label>
            <br />
            <input
              type="text"
              className="form-control"
              name="fname"
              defaultValue={user.fname}
              ref={register({
                required: "* This Field is Required",
                pattern: {
                  value: /^[a-z\s]{0,255}$/i,
                  message: "* Please Enter Valid Name",
                },
              })}
            />
            {errors.fname && <ErrorMsg>{errors.fname.message}</ErrorMsg>}
          </div>
        </div>
        {/* Last Name */}
        <div className="form-group row">
          <div className="col">
            <label>Last Name</label>
            <br />
            <input
              type="text"
              className="form-control"
              name="lname"
              defaultValue={user.lname}
              ref={register({
                pattern: {
                  value: /^[a-z\s]{0,255}$/i,
                  message: "* Please Enter Valid Name",
                },
              })}
            />
            {errors.lname && <ErrorMsg>{errors.lname.message}</ErrorMsg>}
          </div>
        </div>
        {/* Date of Birth */}
        <div className="form-group row">
          <div className="col">
            <label>
              <span className="red-star">*</span> Date of Birth
            </label>
            <br />
            <DatePicker
              className="form-control"
              selected={selectedDate}
              //defaultValue={new Date()}
              onChange={(date) => {
                setSelectedDate(date);
                setErrors_date_of_birth(false);
              }}
              dateFormat="dd/MM/yyyy"
              maxDate={new Date()}
              showYearDropdown
              scrollableMonthYearDropdown
            />
            {errors_date_of_birth && (
              <ErrorMsg>* This Field is Required</ErrorMsg>
            )}
          </div>
        </div>
        {/* Gender */}
        <div className="form-group row">
          <div className="col">
            <label>
              <span className="red-star">*</span> Gender
            </label>
            <br />
            <select
              className="form-control"
              name="gender"
              ref={register({ required: true })}
              value={selectedGender}
              onChange={(e) => setSelectedGender(e.target.value)}
            >
              <option value="Male">Male</option>
              <option value="Female">Female</option>
              <option value="Other">Other</option>
            </select>
            {errors.gender && <ErrorMsg>* This Field is Required</ErrorMsg>}
          </div>
        </div>
        {/* Nationality */}
        <div className="form-group row">
          <div className="col">
            <label>Nationality</label>
            <br />
            <input
              type="text"
              className="form-control"
              name="nationality"
              defaultValue={user.nationality}
              ref={register}
            />
          </div>
        </div>
        {/* Mobile No. */}
        <div className="form-group row">
          <div className="col">
            <label>
              <span className="red-star">*</span> Mobile No.
            </label>
            <br />
            <input
              type="text"
              className="form-control"
              name="mobile"
              defaultValue={user.mobile}
              ref={register({
                required: "* This Field is Required",
                pattern: {
                  value: /^(?:\+88|01)?\d{11}\r?$/,
                  message: "* Please Enter Number",
                },
              })}
            />
            {errors.mobile && <ErrorMsg>{errors.mobile.message}</ErrorMsg>}
          </div>
        </div>
        {/* Designation */}
        <div className="form-group row">
          <div className="col">
            <label>Designation</label>
            <br />
            <input
              type="text"
              className="form-control"
              name="designation"
              defaultValue={user.designation}
              ref={register}
            />
          </div>
        </div>
        {/* Place of Birth */}
        <div className="form-group row">
          <div className="col">
            <label>Place of Birth</label>
            <br />
            <input
              type="text"
              className="form-control"
              name="place_of_birth"
              defaultValue={user.place_of_birth}
              ref={register}
            />
          </div>
        </div>
        {/* Marital Status */}
        <div className="form-group row">
          <div className="col">
            <label>Marital Status</label>
            <br />
            <select
              className="form-control"
              name="marital_status"
              ref={register}
              value={selectedMaritalStatus}
              onChange={(e) => setSelectedMaritalStatus(e.target.value)}
            >
              <option value="Single">Single</option>
              <option value="Married">Married</option>
              <option value="Divorced">Divorced</option>
              <option value="Widowed">Widowed</option>
              <option value="Other">Other</option>
            </select>
          </div>
        </div>
        {/* Religion */}
        <div className="form-group row">
          <div className="col">
            <label>Religion</label>
            <br />
            <select
              className="form-control"
              name="religion"
              defaultValue={user.religion}
              ref={register}
              value={selectedReligion}
              onChange={(e) => setSelectedReligion(e.target.value)}
            >
              <option value="Islam">Islam</option>
              <option value="Hinduism">Hinduism</option>
              <option value="Buddhism">Buddhism</option>
              <option value="Christianity">Christianity</option>
              <option value="Other">Other</option>
            </select>
          </div>
        </div>
        {/* NID */}
        <div className="form-group row">
          <div className="col">
            <label>NID</label>
            <br />
            <input
              type="number"
              min="0"
              className="form-control"
              name="nid"
              defaultValue={user.nid}
              ref={register}
            />
          </div>
        </div>

        <button type="submit" className="btn btn-update">
          Save
        </button>
      </form>
      {/* edit form general info end */}
      <LoadingIndicator />
    </>
  );
}
