import React, { useState } from "react";
import UserPermission from "./UserPermission";
import PackageSetup from "./PackageSetup";
import InstituteType from "./InstituteType";

export default function Settings() {
  const [showUserPermission, setShowUserPermission] = useState(true);
  const [showPackageSetup, setShowPackageSetup] = useState(false);
  const [showInstituteType, setShowInstituteType] = useState(false);
  return (
    <React.Fragment>
      <div className="wrapper">
        {/* PAGE CONTENT START */}
        <div className="content-wrapper pt-3 pb-5">
          <div className="content">
            <div className="container-fluid layout-width">
              {/*  */}
              {/* Content Start */}
              <div className="row">
                <div className="col-12">
                  {/* Profile Edit start */}
                  <div className="row">
                    <div className="col-sm-12 col-md-6 col-lg-6 col-xl-6">
                      <h4 className="heading">Settings</h4>
                    </div>
                  </div>

                  <div className="card">
                    <div className="card-body p-0">
                      <div className="row" style={{ minHeight: "70vh" }}>
                        <div className="col-md-3">
                          <div
                            className="nav flex-column nav-tabs h-100"
                            id="vert-tabs-tab"
                            role="tablist"
                            aria-orientation="vertical"
                            style={{
                              backgroundColor: "#F8F8F8",
                              paddingTop: "40px",
                            }}
                          >
                            <a
                              className={`nav-link edit-profile ${
                                showUserPermission ? "active" : null
                              }`}
                              onClick={() => {
                                setShowUserPermission(true);
                                setShowPackageSetup(false);
                                setShowInstituteType(false);
                              }}
                            >
                              <i className="far fa-user pr-2 pl-3"></i>User
                              Permission
                            </a>
                            <a
                              className={`nav-link edit-profile ${
                                showPackageSetup ? "active" : null
                              }`}
                              onClick={() => {
                                setShowUserPermission(false);
                                setShowPackageSetup(true);
                                setShowInstituteType(false);
                              }}
                            >
                              <i className="far fa-list-alt pr-2 pl-3"></i>
                              Package Setup
                            </a>
                            <a
                              className={`nav-link edit-profile ${
                                showInstituteType ? "active" : null
                              }`}
                              onClick={() => {
                                setShowUserPermission(false);
                                setShowPackageSetup(false);
                                setShowInstituteType(true);
                              }}
                            >
                              <i className="fas fa-university pr-2 pl-3"></i>
                              Institute Type
                            </a>
                          </div>
                        </div>
                        <div
                          className="col-md-9"
                          style={{ padding: "1.25rem" }}
                        >
                          <div
                            className="tab-content"
                            id="vert-tabs-tabContent"
                          >
                            {showUserPermission ? <UserPermission /> : null}
                            {showPackageSetup ? <PackageSetup /> : null}
                            {showInstituteType ? <InstituteType /> : null}
                          </div>
                        </div>
                      </div>
                    </div>
                    {/* /.card */}
                  </div>
                  {/* Profile Edit end */}
                </div>
              </div>
              {/* Content End */}
            </div>
          </div>
        </div>
        {/* PAGE CONTENT END */}
      </div>
    </React.Fragment>
  );
}
