import { Link, useLocation } from "react-router-dom";
import React, { useState, useEffect } from "react";
import axios from "axios";
import { trackPromise } from "react-promise-tracker";
import "react-toastify/dist/ReactToastify.css";
import "../../App.css";
import { toast } from "react-toastify";

export default function ViewEmail({ email_id }) {
  const [data, setData] = useState({});
  const [fileAttachement, setFileAttachement] = useState("");
  const [imageAttachement, setImageAttachement] = useState("");

  // FETCH GetEmailForInbox
  useEffect(() => {
    async function fetchData() {
      let accessToken = localStorage.getItem("token");
      // alert(accessToken);
      accessToken = accessToken.substring(1, accessToken.length - 1);
      var config = {
        method: "post",
        url: "/GetEmailFullDetails",
        headers: {
          Authorization: `Bearer ${accessToken}`,
        },
        data: {
          email_id: email_id,
        },
      };

      const response = await trackPromise(axios(config));
      //console.log("response - " + JSON.stringify(response.data.data));
      let file_attachement = response.data.data[0].file_attachement.replace(
        "http://localhost:3000/lms/",
        ""
      );
      let image_attachement = response.data.data[0].image_attachement.replace(
        "http://localhost:3000/lms/",
        ""
      );
      //console.log("image_attachement " + image_attachement);
      setFileAttachement(file_attachement);
      setImageAttachement(image_attachement);
      setData(response.data.data[0]);
    }
    fetchData();
  }, []);

  async function sendDraftEmail() {
    let accessToken = localStorage.getItem("token");
    //alert(accessToken);
    accessToken = accessToken.substring(1, accessToken.length - 1);
    const emailAddress = localStorage.getItem("emailAddress");
    let formData = new FormData();

    formData.append("email_id", email_id);
    formData.append("to_address", data.to_address);
    formData.append("from_address", emailAddress);
    formData.append("title", data.title);
    formData.append("body", data.body);
    formData.append("status", "sent");
    formData.append(
      "file_attachment",
      "http://18.188.174.161/turzo/files/1603526494564624476_file_example_JPG_100kB.jpg"
    );
    formData.append(
      "image_attachment",
      "http://18.188.174.161/turzo/files/1603526494564624476_file_example_JPG_100kB.jpg"
    );
    var config = {
      method: "post",
      url: "/SendEmailFromDraftBox",
      headers: {
        Authorization: `Bearer ${accessToken}`,
        "Content-Type": "multipart/form-data",
      },
      data: formData,
    };
    console.log(formData);
    const response = await trackPromise(axios(config));
    //console.log("response - " + response.data);
    if (response.data.status === "ok") {
      toast("Email Sent Successfully", {
        className: "toast-error",
      });
    } else {
      toast(response.data.msg, {
        className: "toast-error",
      });
    }
  }
  return (
    <div className="table-responsive">
      <table className="table institute-table table-font-100">
        <tbody>
          <tr>
            <td className="inst-text text-left p-0 m-0">
              <b>To</b>
            </td>
            <td className="border-0 text-left p-0 m-0">: {data.to_address}</td>
          </tr>
          <tr>
            <td className="inst-text text-left p-0 m-0">
              <b>From</b>
            </td>
            <td className="border-0 text-left p-0 m-0">
              : {data.from_address}
            </td>
          </tr>
          <tr>
            <td className="inst-text text-left p-0 m-0">
              <b>Subject</b>
            </td>
            <td className="border-0 text-left p-0 m-0">: {data.title}</td>
          </tr>
          <tr>
            <td className="inst-text text-left p-0 m-0">
              <b>Date</b>
            </td>
            <td className="border-0 text-left p-0 m-0">
              : {data.sending_date}
            </td>
          </tr>

          <tr>
            <td
              className="border-0 text-left"
              style={{ padding: "24px 0px 150px 0px", margin: "0px" }}
              colSpan="2"
            >
              {data.body}
            </td>
          </tr>
          <tr>
            <td
              className="inst-text text-left"
              style={{ padding: "5px", margin: "0px" }}
              colSpan="3"
            >
              {data.status === "draft" ? (
                <button
                  type="submit"
                  className="btn btn-update mr-3 p-0"
                  onClick={() => {
                    sendDraftEmail();
                  }}
                >
                  Send
                </button>
              ) : (
                <span>Attachment: </span>
              )}

              {fileAttachement ? (
                <a href={fileAttachement} target="_blank" download>
                  <i
                    className="far fa-file-pdf pr-2 pl-3"
                    title="File"
                    style={{ fontSize: "20px" }}
                  ></i>
                </a>
              ) : null}
              {imageAttachement ? (
                <a
                  href={imageAttachement}
                  target="_blank"
                  download
                  className="pt-2"
                >
                  <i
                    className="far fa-file-image pr-2 pl-3"
                    title="Image"
                    style={{ fontSize: "20px" }}
                  ></i>
                </a>
              ) : null}
            </td>
          </tr>
          <tr>
            <td
              className="inst-text text-left"
              style={{ padding: "5px", margin: "0px" }}
            ></td>
          </tr>
        </tbody>
        <tfoot className="row"></tfoot>
      </table>
    </div>
  );
}
