import { Link } from "react-router-dom";
import React, { useState, useEffect } from "react";
import { useHistory } from "react-router-dom";
import { useForm } from "react-hook-form";
import axios from "axios";
import { trackPromise } from "react-promise-tracker";
import { LoadingIndicator } from "../Loading/LoadingIndicator";
import styled from "styled-components";
import { toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import "../../App.css";
import Moment from "moment";
const ErrorMsg = styled.p`
  color: red;
  font-size: 14px;
  font-family: "Source Sans Pro", sans-serif;
  font-weight: bold;
`;
export default function AddNewInstType() {
  const { register, handleSubmit, errors } = useForm(); // initialise the hook

  let history = useHistory();
  const onSubmit = (data) => {
    //console.log(data);

    let accessToken = localStorage.getItem("token");
    //alert(accessToken);
    accessToken = accessToken.substring(1, accessToken.length - 1);

    var config = {
      method: "post",
      url: "/SettingsInstituteCreate",
      headers: {
        Authorization: `Bearer ${accessToken}`,
      },
      data: data,
    };

    trackPromise(
      axios(config).then(function (response) {
        // console.log(JSON.stringify(response.data));
        if (response.data.status === "ok") {
          toast("Institute Type Added Successfully", {
            className: "toast-error",
          });
          history.push("settings");
        } else {
          toast(response.data.msg, {
            className: "toast-error",
          });
        }
      })
    );
  };
  return (
    <React.Fragment>
      <div className="wrapper">
        {/* PAGE CONTENT START */}
        <div className="content-wrapper pt-3 pb-5">
          <div className="content">
            <div className="container-fluid layout-width">
              {/* Content Start */}
              <div className="row">
                <div className="col-12">
                  <div className="row">
                    <div className="col-sm-12 col-md-6 col-lg-6 col-xl-6">
                      <h4 className="heading">Add New Institute Type</h4>
                    </div>
                    <div className="col-sm-12 col-md-6 col-lg-6 col-xl-6">
                      <Link
                        to="/settings"
                        className="border ml-1 add-button shadow float-right"
                        title="Back"
                      >
                        <i className="fas fa-arrow-left add-button-icon"></i>
                      </Link>
                    </div>
                  </div>
                  <div className="card">
                    <div className="card-body p-0">
                      <div className="row" style={{ minHeight: "70vh" }}>
                        <div className="col-12" style={{ padding: "2rem" }}>
                          {/* ------------------------------------------------------------------ */}
                          {/* create role & permission form start */}
                          <form
                            className="mt-4"
                            onSubmit={handleSubmit(onSubmit)}
                          >
                            <div className="form-group row">
                              <div
                                className="col-sm-12 col-md-6 col-lg-4 col-xl-4"
                                style={{ marginLeft: "8px" }}
                              >
                                <label>
                                  <span className="red-star">*</span>
                                  Type Name
                                </label>
                                <br />
                                <input
                                  type="text"
                                  className="form-control"
                                  name="institute_name"
                                  ref={register({ required: true })}
                                />
                                {errors.institute_name && (
                                  <ErrorMsg>* This Field is Required</ErrorMsg>
                                )}
                              </div>
                            </div>
                            <div className="form-group row">
                              <div
                                className="col-sm-12 col-md-6 col-lg-4 col-xl-4"
                                style={{ marginLeft: "8px" }}
                              >
                                <label>
                                  <span className="red-star">*</span>
                                  Short Form
                                </label>
                                <br />
                                <input
                                  type="text"
                                  className="form-control"
                                  name="short_form"
                                  ref={register({ required: true })}
                                />
                                {errors.short_form && (
                                  <ErrorMsg>* This Field is Required</ErrorMsg>
                                )}
                              </div>
                            </div>

                            <button
                              type="submit"
                              className="btn btn-update mt-2 mb-2"
                            >
                              Add
                            </button>
                          </form>
                          {/* create role & permission form end */}
                          <LoadingIndicator />
                          {/* ----------------------------------------------------------------------- */}
                        </div>
                      </div>
                    </div>
                    {/* /.card */}
                  </div>
                  {/* Profile Edit end */}
                </div>
              </div>
              {/* Content End */}
            </div>
          </div>
        </div>
        {/* PAGE CONTENT END */}
      </div>
    </React.Fragment>
  );
}
