import React from "react";
import { Link } from "react-router-dom";
import { LoadingIndicator } from "../Loading/LoadingIndicator";
import "react-toastify/dist/ReactToastify.css";
import "../../App.css";
import DatatablePackageSetup from "./DatatablePackageSetup";
export default function PackageSetup() {
  return (
    <div className="row" style={{ minHeight: "70vh" }}>
      <div className="col-12" style={{ padding: "2rem" }}>
        <div className="d-flex float-left mb-3">
          <Link
            to="/add-new-pkg-item"
            className="border ml-1 add-button shadow"
            title="Add New Package Item"
          >
            <i className="fas fa-plus add-button-icon"></i>
          </Link>
        </div>
        <DatatablePackageSetup />
        <LoadingIndicator />
      </div>
    </div>
  );
}
