import React, { useState, useEffect, useMemo } from "react";
import { Link } from "react-router-dom";
import axios from "axios";
import { trackPromise } from "react-promise-tracker";
import { LoadingIndicator } from "../Loading/LoadingIndicator";
import "react-toastify/dist/ReactToastify.css";
import "../../App.css";
import DatatableBillingList from "./DatatableBillingList";
import DatatableBillingHistoryList from "./DatatableBillingHistoryList";
import PaginationComponent from "../Pagination/PaginationComponent";
export default function BillingListView() {
  const [data, setData] = useState([]);
  //GET ALL BILLING LIST -START
  useEffect(() => {
    async function fetchData() {
      let accessToken = localStorage.getItem("token");
      // alert(accessToken);
      accessToken = accessToken.substring(1, accessToken.length - 1);
      var config = {
        method: "get",
        url: "/GetAllInvoiceList",
        headers: {
          Authorization: `Bearer ${accessToken}`,
        },
      };

      const billingListApiResp = await trackPromise(axios(config));
      // console.log(
      //   "billingListApiResp - " +
      //     JSON.stringify(billingListApiResp.data.msg.data)
      // );
      /*BILLING STATUS FILTER BY INVOICE STATUS == PENDING  START*/
      let jsObjects = billingListApiResp.data.msg.data;
      let result = jsObjects.filter((obj) => {
        return obj.status === "Pending";
      });
      //console.log(result);
      /*BILLING STATUS FILTER BY INVOICE STATUS == PENDING  END*/

      setData(result);
    }
    fetchData();
  }, []);
  //GET ALL BILLING LIST -END

  /*SEARCH AND PAGINATION CODE START*/
  const [search, setSearch] = useState("");
  const [totalItems, setTotalItems] = useState(0);
  const [currentPage, setCurrentPage] = useState(1);
  const ITEMS_PER_PAGE = 20;

  const currentData = useMemo(() => {
    let computedData = data; //data = response data

    /*SEARCH CODE START */
    if (search) {
      computedData = computedData.filter(
        (d) =>
          d.id.toLowerCase().includes(search.toLowerCase()) ||
          d.institute_id.toLowerCase().includes(search.toLowerCase()) ||
          d.institute_name.toLowerCase().includes(search.toLowerCase()) ||
          d.month.toLowerCase().includes(search.toLowerCase()) ||
          d.year.toLowerCase().includes(search.toLowerCase()) ||
          d.amount.toLowerCase().includes(search.toLowerCase())
      );
    }
    /*SEARCH CODE END */

    setTotalItems(computedData.length);
    return computedData.slice(
      (currentPage - 1) * ITEMS_PER_PAGE,
      (currentPage - 1) * ITEMS_PER_PAGE + ITEMS_PER_PAGE
    );
  }, [data, currentPage, search]);
  //console.log("totalItems " + totalItems);
  /*SEARCH AND PAGINATION CODE END*/

  return (
    <div>
      <div className="wrapper">
        {/* PAGE CONTENT START */}
        <div className="content-wrapper pt-3 pb-5">
          <div className="content">
            <div className="container-fluid layout-width">
              {/*  */}
              {/* Content Start */}
              <div className="row">
                <div className="col-12">
                  <div className="row">
                    <div className="col-12">
                      <h4 className="heading">Billing Information</h4>
                    </div>
                  </div>
                  {/* start here */}
                  <div className="card card-primary card-outline card-outline-tabs">
                    <div className="card-header p-0 border-bottom-0">
                      <ul
                        className="nav nav-tabs"
                        id="custom-tabs-four-tab"
                        role="tablist"
                      >
                        <li className="nav-item">
                          <a
                            className="nav-link active"
                            id="custom-tabs-four-home-tab"
                            data-toggle="pill"
                            href="#custom-tabs-four-home"
                            role="tab"
                            aria-controls="custom-tabs-four-home"
                            aria-selected="true"
                          >
                            <span className="border-bottom-red">
                              Invoice List
                            </span>
                          </a>
                        </li>
                        <li className="nav-item">
                          <a
                            className="nav-link"
                            id="custom-tabs-four-profile-tab"
                            data-toggle="pill"
                            href="#custom-tabs-four-profile"
                            role="tab"
                            aria-controls="custom-tabs-four-profile"
                            aria-selected="false"
                          >
                            <span className="border-bottom-red">
                              Invoice History
                            </span>
                          </a>
                        </li>
                      </ul>
                    </div>
                    <div className="card-body">
                      <div
                        className="tab-content"
                        id="custom-tabs-four-tabContent"
                      >
                        <div
                          className="tab-pane fade show active"
                          id="custom-tabs-four-home"
                          role="tabpanel"
                          aria-labelledby="custom-tabs-four-home-tab"
                        >
                          {/* 1st content here */}
                          <div className="row" style={{ minHeight: "70vh" }}>
                            <div className="col-12">
                              <div className="d-flex float-left mb-3">
                                <Link
                                  to="/create-bill"
                                  className="border ml-1 add-button shadow"
                                  title="Create Bill"
                                >
                                  <i className="fas fa-plus add-button-icon"></i>
                                </Link>
                              </div>
                              <div className="d-flex float-right mb-3">
                                <input
                                  type="text"
                                  className="inst-list-search"
                                  value={search}
                                  onChange={(e) => {
                                    setSearch(e.target.value);
                                    setCurrentPage(1);
                                  }}
                                />
                                <div className="border ml-1 inst-list-search-div">
                                  <i className="fas fa-search serach-icon-inst-list"></i>
                                </div>
                              </div>
                              <DatatableBillingList data={currentData} />
                              <PaginationComponent
                                total={totalItems}
                                itemsPerPage={ITEMS_PER_PAGE}
                                currentPage={currentPage}
                                onPageChange={(page) => setCurrentPage(page)}
                              />
                              <LoadingIndicator />
                            </div>
                          </div>
                        </div>
                        <div
                          className="tab-pane fade"
                          id="custom-tabs-four-profile"
                          role="tabpanel"
                          aria-labelledby="custom-tabs-four-profile-tab"
                        >
                          {/* 2nd content here */}
                          <div className="row" style={{ minHeight: "70vh" }}>
                            <div className="col-12">
                              <div className="d-flex float-right mb-3">
                                <input
                                  type="text"
                                  className="inst-list-search"
                                  //value={q}
                                  //onChange={(e) => setQ(e.target.value)}
                                />
                                <div className="border ml-1 inst-list-search-div">
                                  <i className="fas fa-search serach-icon-inst-list"></i>
                                </div>
                              </div>
                              <DatatableBillingHistoryList />
                              <LoadingIndicator />
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                    {/* /.card */}
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
