import React, { useState } from "react";
import { Link } from "react-router-dom";
import { useLocation } from "react-router";
import UpdateUserAvatar from "./UpdateUserAvatar";
import UpdateUserCover from "./UpdateUserCover";
import UpdateInformation from "./UpdateInformation";
export default function UpdateUser() {
  const location = useLocation();
  //console.log(location);

  const [showEdit, setShowEdit] = useState(true);
  const [showProfilePhoto, setShowProfilePhoto] = useState(false);
  const [showCoverPhoto, setShowCoverPhoto] = useState(false);
  return (
    <React.Fragment>
      <div className="wrapper">
        {/* PAGE CONTENT START */}
        <div className="content-wrapper pt-3 pb-5">
          <div className="content">
            <div className="container-fluid layout-width">
              {/* Content Start */}
              <div className="row">
                <div className="col-12">
                  {/* Profile Update start */}
                  <div className="row">
                    <div className="col-sm-12 col-md-6 col-lg-6 col-xl-6">
                      <h4 style={{ marginBottom: "32px", color: "black" }}>
                        Update Profile
                      </h4>
                    </div>
                  </div>

                  <div className="card">
                    <div className="card-body p-0">
                      <div className="row" style={{ minHeight: "70vh" }}>
                        <div className="col-md-3">
                          <div
                            className="nav flex-column nav-tabs h-100"
                            id="vert-tabs-tab"
                            role="tablist"
                            aria-orientation="vertical"
                            style={{
                              backgroundColor: "#F8F8F8",
                              paddingTop: "40px",
                            }}
                          >
                            <a
                              className={`nav-link edit-profile ${
                                showEdit ? "active" : null
                              }`}
                              onClick={() => {
                                setShowEdit(true);
                                setShowProfilePhoto(false);
                                setShowCoverPhoto(false);
                                //console.log("Edit true");
                              }}
                            >
                              <i className="far fa-edit pr-2 pl-3"></i>Update
                            </a>
                            <a
                              className={`nav-link edit-profile ${
                                showProfilePhoto ? "active" : null
                              }`}
                              onClick={() => {
                                setShowEdit(false);
                                setShowProfilePhoto(true);
                                setShowCoverPhoto(false);
                                //console.log("Profile Photo true");
                              }}
                            >
                              <i className="far fa-address-card pr-2 pl-3"></i>
                              Profile Photo
                            </a>
                            <a
                              className={`nav-link edit-profile ${
                                showCoverPhoto ? "active" : null
                              }`}
                              onClick={() => {
                                setShowEdit(false);
                                setShowProfilePhoto(false);
                                setShowCoverPhoto(true);
                                //console.log("Cover Photo true");
                              }}
                            >
                              <i className="far fa-image pr-2 pl-3"></i>Cover
                              Photo
                            </a>
                          </div>
                        </div>
                        <div
                          className="col-md-9"
                          style={{ padding: "1.25rem" }}
                        >
                          <div
                            className="tab-content"
                            id="vert-tabs-tabContent"
                          >
                            {showEdit ? (
                              <UpdateInformation location={location} />
                            ) : null}
                            {showProfilePhoto ? (
                              <UpdateUserAvatar email={location.state.email} />
                            ) : null}
                            {showCoverPhoto ? (
                              <UpdateUserCover email={location.state.email} />
                            ) : null}
                          </div>
                        </div>
                      </div>
                    </div>
                    {/* /.card */}
                  </div>
                  {/* Profile Update end */}
                </div>
              </div>
              {/* Content End */}
            </div>
          </div>
        </div>
        {/* PAGE CONTENT END */}
      </div>
    </React.Fragment>
  );
}
