import React, { useState, useEffect, useMemo } from "react";
import { Link } from "react-router-dom";
import { useLocation } from "react-router";
import axios from "axios";
import { trackPromise } from "react-promise-tracker";
import { LoadingIndicator } from "../Loading/LoadingIndicator";

import "react-toastify/dist/ReactToastify.css";
import "../../App.css";
import DatatableInstPaymentHistory from "./DatatableInstPaymentHistory";
export default function InstPaymentHistory() {
  const [data, setData] = useState([]);
  const [instData, setInstData] = useState([]);

  const location = useLocation();
  //console.log(location);

  //GET ALL PACKAGE NAME -START
  useEffect(() => {
    async function fetchData() {
      let accessToken = localStorage.getItem("token");
      // alert(accessToken);
      accessToken = accessToken.substring(1, accessToken.length - 1);
      var config = {
        method: "post",
        url: "/GetAllInvoicesSingleInstitute",
        headers: {
          Authorization: `Bearer ${accessToken}`,
        },
        data: {
          institute_id_string: location.state.institute_id_string,
        },
      };

      const invoiceApiResp = await trackPromise(axios(config));
      // console.log(
      //   "invoiceApiResp - " + JSON.stringify(invoiceApiResp.data.msg.details)
      // );

      setInstData(invoiceApiResp.data.msg.details);
      setData(invoiceApiResp.data.msg.data);
    }
    fetchData();
  }, []);
  //GET ALL PACKAGE NAME -END

  /*SEARCH AND PAGINATION CODE START*/
  const [search, setSearch] = useState("");
  const [totalItems, setTotalItems] = useState(0);
  const [currentPage, setCurrentPage] = useState(1);
  const ITEMS_PER_PAGE = 20;

  const currentData = useMemo(() => {
    let computedData = data; //data = response data

    /*SEARCH CODE START */
    if (search) {
      computedData = computedData.filter(
        (d) =>
          d.id.toLowerCase().includes(search.toLowerCase()) ||
          d.institute_id.toLowerCase().includes(search.toLowerCase()) ||
          d.institute_name.toLowerCase().includes(search.toLowerCase()) ||
          d.month.toLowerCase().includes(search.toLowerCase()) ||
          d.year.toLowerCase().includes(search.toLowerCase()) ||
          d.amount.toLowerCase().includes(search.toLowerCase())
      );
    }
    /*SEARCH CODE END */

    setTotalItems(computedData.length);
    return computedData.slice(
      (currentPage - 1) * ITEMS_PER_PAGE,
      (currentPage - 1) * ITEMS_PER_PAGE + ITEMS_PER_PAGE
    );
  }, [data, currentPage, search]);
  //console.log("totalItems " + totalItems);
  /*SEARCH AND PAGINATION CODE END*/

  return (
    <div>
      <div className="wrapper">
        {/* PAGE CONTENT START */}
        <div className="content-wrapper pt-3 pb-5">
          <div className="content">
            <div className="container-fluid layout-width">
              {/*  */}
              {/* Content Start */}
              <div className="row">
                <div className="col-12">
                  <div className="row">
                    <div className="col-12">
                      <h4 className="heading">Institute Payment History</h4>
                    </div>
                  </div>

                  <div className="card">
                    <div className="card-body p-0">
                      <div className="row" style={{ minHeight: "70vh" }}>
                        <div className="col-12" style={{ padding: "2rem" }}>
                          <table className="table table-sm table-borderless table-responsive">
                            <tbody className="p-0 m-0">
                              <tr>
                                <td className="pl-0">Institute ID</td>
                                <td className="pl-0 pr-0">
                                  {instData.institute_id}
                                </td>
                              </tr>
                              <tr>
                                <td className="pl-0">Institute Name</td>
                                <td className="pl-0 pr-0">{instData.name}</td>
                              </tr>
                              <tr>
                                <td className="pl-0">Contact</td>
                                <td className="pl-0 pr-0">{instData.phone}</td>
                              </tr>
                              <tr className="bottom-border">
                                <td className="pl-0">Email</td>
                                <td className="pl-0 pr-0">{instData.email}</td>
                              </tr>
                            </tbody>
                          </table>

                          <div className="d-flex float-right mb-3">
                            <input
                              type="text"
                              className="inst-list-search"
                              value={search}
                              onChange={(e) => {
                                setSearch(e.target.value);
                                setCurrentPage(1);
                              }}
                            />
                            <div className="border ml-1 inst-list-search-div">
                              <i className="fas fa-search serach-icon-inst-list"></i>
                            </div>
                          </div>
                          <DatatableInstPaymentHistory data={currentData} />
                          <LoadingIndicator />
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
