import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import axios from "axios";
import { trackPromise } from "react-promise-tracker";
import { LoadingIndicator } from "../Loading/LoadingIndicator";

import "react-toastify/dist/ReactToastify.css";
import "../../App.css";
import DatatableUserTypeList from "./DatatableUserTypeList";
export default function UserPermission() {
  const [data, setData] = useState([]);

  //GET USERTYPE LIST -START
  useEffect(() => {
    async function fetchData() {
      let accessToken = localStorage.getItem("token");
      // alert(accessToken);
      accessToken = accessToken.substring(1, accessToken.length - 1);
      var config = {
        method: "get",
        url: "/GetUserTypeList",
        headers: {
          Authorization: `Bearer ${accessToken}`,
        },
      };

      const userTypeApiResp = await trackPromise(axios(config));
      // console.log(
      //   "userTypeApiResp - " + JSON.stringify(userTypeApiResp.data.data)
      // );
      setData(userTypeApiResp.data.data);
    }
    fetchData();
  }, []);
  //GET USERTYPE LIST -END

  return (
    <div className="row" style={{ minHeight: "70vh" }}>
      <div className="col-12" style={{ padding: "2rem" }}>
        <div className="d-flex float-left mb-3">
          <Link
            to="/create-user-type"
            className="border ml-1 add-button shadow"
            title="Create User Type"
          >
            <i className="fas fa-plus add-button-icon"></i>
          </Link>
        </div>
        <DatatableUserTypeList data={data} />
        <LoadingIndicator />
      </div>
    </div>
  );
}
