import React from "react";
import { Link } from "react-router-dom";
import CreateBillForm from "./CreateBillForm";
export default function CreateBill() {
  return (
    <React.Fragment>
      <div className="wrapper">
        {/* PAGE CONTENT START */}
        <div className="content-wrapper pt-3 pb-5">
          <div className="content">
            <div className="container-fluid layout-width">
              {/* Content Start */}
              <div className="row">
                <div className="col-12">
                  {/* Profile Create start */}
                  <div className="row">
                    <div className="col-sm-12 col-md-6 col-lg-6 col-xl-6">
                      <h4 className="heading">Generate Bill</h4>
                    </div>
                    <div className="col-sm-12 col-md-6 col-lg-6 col-xl-6">
                      <Link
                        to="/billing-list-view"
                        className="border ml-1 add-button shadow float-right"
                        title="Back"
                      >
                        <i className="fas fa-arrow-left add-button-icon"></i>
                      </Link>
                    </div>
                  </div>

                  <div className="card">
                    <div className="card-body p-0">
                      <div className="row" style={{ minHeight: "70vh" }}>
                        <div className="col-12" style={{ padding: "2rem" }}>
                          <CreateBillForm />
                        </div>
                      </div>
                    </div>
                    {/* /.card */}
                  </div>
                  {/* Profile create end */}
                </div>
              </div>
              {/* Content End */}
            </div>
          </div>
        </div>
        {/* PAGE CONTENT END */}
      </div>
    </React.Fragment>
  );
}
