import { Link } from "react-router-dom";
import React, { useState, useEffect, useMemo } from "react";
import { useHistory } from "react-router-dom";
import { useForm } from "react-hook-form";
import axios from "axios";
import { trackPromise } from "react-promise-tracker";
import { LoadingIndicator } from "../Loading/LoadingIndicator";
import PaginationComponent from "../Pagination/PaginationComponent";
import styled from "styled-components";
import { toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import "../../App.css";
import Moment from "moment";
import DatatablePkgList from "./DatatablePkgList";
export default function PackageListView() {
  const [data, setData] = useState([]);

  //GET ALL PACKAGE NAME -START
  useEffect(() => {
    async function fetchData() {
      let accessToken = localStorage.getItem("token");
      // alert(accessToken);
      accessToken = accessToken.substring(1, accessToken.length - 1);
      var config = {
        method: "get",
        url: "/all-packages",
        headers: {
          Authorization: `Bearer ${accessToken}`,
        },
      };

      let allPkgApiResp = await trackPromise(axios(config));
      allPkgApiResp = allPkgApiResp.data.msg.reverse();
      // console.log("allPkgApiResp - " + JSON.stringify(allPkgApiResp));
      // formatObject(allPkgApiResp);
      setData(allPkgApiResp);
    }
    fetchData();
  }, []);
  //GET ALL PACKAGE NAME -END

  /*SEARCH AND PAGINATION CODE START*/
  const [search, setSearch] = useState("");
  const [totalItems, setTotalItems] = useState(0);
  const [currentPage, setCurrentPage] = useState(1);
  const ITEMS_PER_PAGE = 20;

  const currentData = useMemo(() => {
    let computedData = data; //data = response data

    /*SEARCH CODE START */
    if (search) {
      computedData = computedData.filter(
        (d) =>
          d.title.toLowerCase().includes(search.toLowerCase()) ||
          d.type.toLowerCase().includes(search.toLowerCase()) ||
          d.display.toLowerCase().includes(search.toLowerCase())
      );
    }
    /*SEARCH CODE END */

    setTotalItems(computedData.length);
    return computedData.slice(
      (currentPage - 1) * ITEMS_PER_PAGE,
      (currentPage - 1) * ITEMS_PER_PAGE + ITEMS_PER_PAGE
    );
  }, [data, currentPage, search]);
  //console.log("totalItems " + totalItems);
  /*SEARCH AND PAGINATION CODE END*/

  return (
    <div>
      <div className="wrapper">
        {/* PAGE CONTENT START */}
        <div className="content-wrapper pt-3 pb-5">
          <div className="content">
            <div className="container-fluid layout-width">
              {/*  */}
              {/* Content Start */}
              <div className="row">
                <div className="col-12">
                  <div className="row">
                    <div className="col-sm-12 col-md-6 col-lg-6 col-xl-6">
                      <h4 className="heading">Package List</h4>
                    </div>
                  </div>

                  <div className="card">
                    <div className="card-body p-0">
                      <div className="row" style={{ minHeight: "70vh" }}>
                        <div className="col-12" style={{ padding: "2rem" }}>
                          <div className="d-flex float-left mb-3">
                            <Link
                              to="/package-create"
                              className="border ml-1 add-button shadow"
                              title="Create Package"
                            >
                              <i className="fas fa-plus add-button-icon"></i>
                            </Link>
                          </div>
                          <div className="d-flex float-right mb-3">
                            <input
                              type="text"
                              className="inst-list-search"
                              value={search}
                              onChange={(e) => {
                                setSearch(e.target.value);
                                setCurrentPage(1);
                              }}
                            />
                            <div className="border ml-1 inst-list-search-div">
                              <i className="fas fa-search serach-icon-inst-list"></i>
                            </div>
                          </div>
                          <DatatablePkgList data={currentData} />
                          <PaginationComponent
                            total={totalItems}
                            itemsPerPage={ITEMS_PER_PAGE}
                            currentPage={currentPage}
                            onPageChange={(page) => setCurrentPage(page)}
                          />
                          <LoadingIndicator />
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
