import React, { useState } from "react";
import UpdateUserProfile from "./UpdateUserProfile";
import UpdateUserAddress from "./UpdateUserAddress";

export default function UpdateInformation({ location }) {
  //console.log(location);
  const [showGeneralInfo, setShowGeneralInfo] = useState(true);
  const [showAddressInfo, setShowAddressInfo] = useState(false);
  return (
    <div className="card-body">
      <h4>Update Information</h4>
      <ul className="nav nav-tabs" id="custom-content-below-tab" role="tablist">
        <li className="nav-item">
          <a
            className={`nav-link ${
              showGeneralInfo ? "active border-red" : null
            }`}
            onClick={() => {
              setShowGeneralInfo(true);
              setShowAddressInfo(false);
              //console.log("GeneralInfo true");
            }}
            style={{
              padding: "0.5rem 0rem",
              marginRight: "40px",
            }}
          >
            <span className="border-bottom-red">General Information</span>
          </a>
        </li>
        <li className="nav-item">
          <a
            className={`nav-link ${
              showAddressInfo ? "active border-red" : null
            }`}
            onClick={() => {
              setShowGeneralInfo(false);
              setShowAddressInfo(true);
              //console.log("AddressInfo true");
            }}
            style={{
              padding: "0.5rem 0rem",
            }}
          >
            <span className="border-bottom-red">Address Information</span>
          </a>
        </li>
      </ul>
      <div className="tab-content" id="custom-content-below-tabContent">
        {showGeneralInfo ? (
          <UpdateUserProfile email={location.state.email} />
        ) : null}
        {showAddressInfo ? (
          <UpdateUserAddress email={location.state.email} />
        ) : null}
      </div>
    </div>
  );
}
