import React from "react";
import { Link } from "react-router-dom";
import Moment from "moment";
export default function DatatableInstDetail({ data }) {
  //console.log("Institute Details: " + JSON.stringify(data));

  return (
    <div className="table-responsive">
      <table className="table institute-table col-sm-12 col-md-8 col-lg-8 col-xl-8 table-font-100">
        <tbody>
          <tr>
            <td className="inst-text text-left">
              <b>Institute ID</b>
            </td>
            <td className="border-0 text-left">: {data.institute_id}</td>
          </tr>
          <tr>
            <td className="inst-text text-left">
              <b>Institute Name</b>
            </td>
            <td className="border-0 text-left">: {data.name}</td>
          </tr>

          <tr>
            <td className="inst-text text-left">
              <b>Status</b>
            </td>
            <td className="border-0 text-left">
              :{" "}
              {data.active === "1" ? (
                <span className="text-green">Active</span>
              ) : (
                <span className="text-red">Inactive</span>
              )}
            </td>
          </tr>
          <tr>
            <td className="inst-text text-left">
              <b>Email Address</b>
            </td>
            <td className="border-0 text-left">: {data.email}</td>
          </tr>
          <tr>
            <td className="inst-text text-left">
              <b>Phone / Mobile No.</b>
            </td>
            <td className="border-0 text-left">: {data.phone}</td>
          </tr>
          <tr>
            <td className="inst-text text-left">
              <b>Address</b>
            </td>
            <td className="border-0 text-left">: {data.address}</td>
          </tr>
          <tr>
            <td className="inst-text text-left">
              <b>Package Name</b>
            </td>
            <td className="border-0 text-left">
              <Link to={`/package-details/${data.package_id}`}>
                : {data.package_title}
              </Link>
            </td>
          </tr>
          <tr>
            <td className="inst-text text-left">
              <b>Subscription Start Date</b>
            </td>
            <td className="border-0 text-left">
              : {Moment(data.subscription_s_date).format("DD-MM-YYYY")}
            </td>
          </tr>
          <tr>
            <td className="inst-text text-left">
              <b>Subscription End Date</b>
            </td>
            <td className="border-0 text-left">
              : {Moment(data.subscription_e_date).format("DD-MM-YYYY")}
            </td>
          </tr>
          <tr>
            <td className="inst-text text-left">
              <b>Payable Amount</b>
            </td>
            <td className="border-0 text-left">: {data.payment_amount}</td>
          </tr>
        </tbody>
      </table>
    </div>
  );
}
