import { Link, useLocation } from "react-router-dom";
import React, { useState, useEffect } from "react";
import { useHistory } from "react-router-dom";
import { useForm } from "react-hook-form";
import axios from "axios";
import { trackPromise } from "react-promise-tracker";
import { LoadingIndicator } from "../Loading/LoadingIndicator";
import styled from "styled-components";
import { toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import "../../App.css";
import Moment from "moment";
const ErrorMsg = styled.p`
  color: red;
  font-size: 14px;
  font-family: "Source Sans Pro", sans-serif;
  font-weight: bold;
`;
export default function EditUserType() {
  const { register, handleSubmit, errors } = useForm();
  const [permissionError, setPermissionError] = useState(false);

  let accessToken = localStorage.getItem("token");
  //alert(accessToken);
  accessToken = accessToken.substring(1, accessToken.length - 1);

  let history = useHistory();

  // POST ROLE & PERMISSION DATA -START
  const onSubmit = (/*data*/) => {
    //console.log(JSON.stringify(data));
    // console.log("user value- " + user);
    // console.log("institute value- " + institute);
    // console.log("package value- " + pkg);
    // console.log("bill value- " + bill);
    // console.log("payment value- " + payment);

    if (
      user === "0000" &&
      institute === "0000" &&
      pkg === "0000" &&
      bill === "0000" &&
      payment === "0000"
    ) {
      setPermissionError(true);
    } else {
      setPermissionError(false);
      data["user"] = user;
      data["institute"] = institute;
      data["package"] = pkg;
      data["bill"] = bill;
      data["payment"] = payment;

      //console.log(JSON.stringify(data));

      var config = {
        method: "post",
        url: "/UpdateUserType",
        headers: {
          Authorization: `Bearer ${accessToken}`,
        },
        data: data,
      };

      trackPromise(
        axios(config).then(function (response) {
          // console.log(JSON.stringify(response.data));
          if (response.data.status === "ok") {
            toast("Permission Updated Successfully", {
              className: "toast-error",
            });
            history.push("/settings");
          } else {
            toast(response.data.msg, {
              className: "toast-error",
            });
          }
        })
      );
    }
  };
  // POST ROLE & PERMISSION DATA -END

  const [data, setData] = useState([]);

  const [user, setUser] = useState("0000");
  const [institute, setInstitute] = useState("0000");
  const [pkg, setPkg] = useState("0000");
  const [bill, setBill] = useState("0000");
  const [payment, setPayment] = useState("0000");

  const location = useLocation();
  //console.log(location.state.id);

  // FETCH USERTYPE LIST
  useEffect(() => {
    async function fetchData() {
      let accessToken = localStorage.getItem("token");
      // alert(accessToken);
      accessToken = accessToken.substring(1, accessToken.length - 1);
      var config = {
        method: "post",
        url: "/ViewSingleUserType",
        headers: {
          Authorization: `Bearer ${accessToken}`,
        },
        data: {
          id: location.state.id,
        },
      };

      const response = await trackPromise(axios(config));
      //console.log("response - " + JSON.stringify(response.data.msg));
      setData(response.data.msg);
      setUser(response.data.msg.user);
      setInstitute(response.data.msg.institute);
      setPkg(response.data.msg.package);
      setBill(response.data.msg.bill);
      setPayment(response.data.msg.payment);
    }
    fetchData();
  }, []);

  function setCharAt(str, index, chr) {
    if (index > str.length - 1) return str;
    return str.substring(0, index) + chr + str.substring(index + 1);
  }

  /*USER PERMISSION START */
  function toggleUserCreate() {
    user.charAt(0) === "1"
      ? setUser(setCharAt(user, 0, "0"))
      : setUser(setCharAt(user, 0, "1"));
  }
  function toggleUserView() {
    user.charAt(1) === "1"
      ? setUser(setCharAt(user, 1, "0"))
      : setUser(setCharAt(user, 1, "1"));
  }
  function toggleUserUpdate() {
    user.charAt(2) === "1"
      ? setUser(setCharAt(user, 2, "0"))
      : setUser(setCharAt(user, 2, "1"));
  }
  function toggleUserDelete() {
    user.charAt(3) === "1"
      ? setUser(setCharAt(user, 3, "0"))
      : setUser(setCharAt(user, 3, "1"));
  }
  function toggleUserAll() {
    user === "1111" ? setUser("0000") : setUser("1111");
  }
  /*USER PERMISSION END */

  /*INSTITUTE PERMISSION START */
  function toggleInstCreate() {
    institute.charAt(0) === "1"
      ? setInstitute(setCharAt(institute, 0, "0"))
      : setInstitute(setCharAt(institute, 0, "1"));
  }
  function toggleInstView() {
    institute.charAt(1) === "1"
      ? setInstitute(setCharAt(institute, 1, "0"))
      : setInstitute(setCharAt(institute, 1, "1"));
  }
  function toggleInstUpdate() {
    institute.charAt(2) === "1"
      ? setInstitute(setCharAt(institute, 2, "0"))
      : setInstitute(setCharAt(institute, 2, "1"));
  }
  function toggleInstDelete() {
    institute.charAt(3) === "1"
      ? setInstitute(setCharAt(institute, 3, "0"))
      : setInstitute(setCharAt(institute, 3, "1"));
  }
  function toggleInstAll() {
    institute === "1111" ? setInstitute("0000") : setInstitute("1111");
  }
  /*INSTITUTE PERMISSION END */

  /*PACKAGE PERMISSION START */
  function togglePkgCreate() {
    pkg.charAt(0) === "1"
      ? setPkg(setCharAt(pkg, 0, "0"))
      : setPkg(setCharAt(pkg, 0, "1"));
  }
  function togglePkgView() {
    pkg.charAt(1) === "1"
      ? setPkg(setCharAt(pkg, 1, "0"))
      : setPkg(setCharAt(pkg, 1, "1"));
  }
  function togglePkgUpdate() {
    pkg.charAt(2) === "1"
      ? setPkg(setCharAt(pkg, 2, "0"))
      : setPkg(setCharAt(pkg, 2, "1"));
  }
  function togglePkgDelete() {
    pkg.charAt(3) === "1"
      ? setPkg(setCharAt(pkg, 3, "0"))
      : setPkg(setCharAt(pkg, 3, "1"));
  }
  function togglePkgAll() {
    pkg === "1111" ? setPkg("0000") : setPkg("1111");
  }
  /*PACKAGE PERMISSION END */

  /*BILL PERMISSION START */
  function toggleBillCreate() {
    bill.charAt(0) === "1"
      ? setBill(setCharAt(bill, 0, "0"))
      : setBill(setCharAt(bill, 0, "1"));
  }
  function toggleBillView() {
    bill.charAt(1) === "1"
      ? setBill(setCharAt(bill, 1, "0"))
      : setBill(setCharAt(bill, 1, "1"));
  }
  function toggleBillUpdate() {
    bill.charAt(2) === "1"
      ? setBill(setCharAt(bill, 2, "0"))
      : setBill(setCharAt(bill, 2, "1"));
  }
  function toggleBillDelete() {
    bill.charAt(3) === "1"
      ? setBill(setCharAt(bill, 3, "0"))
      : setBill(setCharAt(bill, 3, "1"));
  }
  function toggleBillAll() {
    bill === "1111" ? setBill("0000") : setBill("1111");
  }
  /*BILL PERMISSION END */

  /*PAYMENT PERMISSION START */
  function togglePaymentCreate() {
    payment.charAt(0) === "1"
      ? setPayment(setCharAt(payment, 0, "0"))
      : setPayment(setCharAt(payment, 0, "1"));
  }
  function togglePaymentView() {
    payment.charAt(1) === "1"
      ? setPayment(setCharAt(payment, 1, "0"))
      : setPayment(setCharAt(payment, 1, "1"));
  }
  function togglePaymentUpdate() {
    payment.charAt(2) === "1"
      ? setPayment(setCharAt(payment, 2, "0"))
      : setPayment(setCharAt(payment, 2, "1"));
  }
  function togglePaymentDelete() {
    payment.charAt(3) === "1"
      ? setPayment(setCharAt(payment, 3, "0"))
      : setPayment(setCharAt(payment, 3, "1"));
  }
  function togglePaymentAll() {
    payment === "1111" ? setPayment("0000") : setPayment("1111");
  }
  /*PAYMENT PERMISSION END */
  return (
    <React.Fragment>
      <div className="wrapper">
        {/* PAGE CONTENT START */}
        <div className="content-wrapper pt-3 pb-5">
          <div className="content">
            <div className="container-fluid layout-width">
              {/* Content Start */}
              <div className="row">
                <div className="col-12">
                  <div className="row">
                    <div className="col-sm-12 col-md-6 col-lg-6 col-xl-6">
                      <h4 className="heading">Update User Type</h4>
                    </div>
                    <div className="col-sm-12 col-md-6 col-lg-6 col-xl-6">
                      <Link
                        to="/settings"
                        className="border ml-1 add-button shadow float-right"
                        title="Back"
                      >
                        <i className="fas fa-arrow-left add-button-icon"></i>
                      </Link>
                    </div>
                  </div>
                  <div className="card">
                    <div className="card-body p-0">
                      <div className="row" style={{ minHeight: "70vh" }}>
                        <div className="col-12" style={{ padding: "2rem" }}>
                          {/* ------------------------------------------------------------------ */}
                          {/* update role & permission form start */}
                          <form
                            className="mt-4"
                            onSubmit={handleSubmit(onSubmit)}
                          >
                            <div className="form-group row">
                              <div
                                className="col-sm-12 col-md-6 col-lg-4 col-xl-4"
                                style={{ marginLeft: "8px" }}
                              >
                                <label>
                                  <span className="red-star">*</span>
                                  User Type
                                </label>
                                <br />
                                <input
                                  type="text"
                                  className="form-control"
                                  name="typename"
                                  readOnly
                                  defaultValue={data.typename}
                                  ref={register({ required: true })}
                                />
                                {errors.typename && (
                                  <ErrorMsg>* This Field is Required</ErrorMsg>
                                )}
                              </div>
                            </div>

                            {/* ************************************************************ */}
                            <div className="col-sm-12 col-md-6 col-lg-4 col-xl-4">
                              <label>Select Permission</label>
                            </div>
                            <div className="table-responsive">
                              <table className="table table-hover institute-table">
                                <thead>
                                  <tr className="inst-list-heading">
                                    <th className="inst-list-th text-center">
                                      Feature Name
                                    </th>
                                    <th className="inst-list-th text-center">
                                      Create
                                    </th>
                                    <th className="inst-list-th text-center">
                                      View
                                    </th>
                                    <th className="inst-list-th text-center">
                                      Update
                                    </th>
                                    <th className="inst-list-th text-center">
                                      Delete
                                    </th>
                                    <th className="inst-list-th text-center">
                                      All
                                    </th>
                                  </tr>
                                </thead>
                                <tbody>
                                  {/* USER */}
                                  <tr>
                                    <td className="inst-list-td text-center">
                                      User
                                    </td>
                                    <td className="inst-list-td text-center">
                                      <input
                                        type="checkbox"
                                        checked={
                                          user.charAt(0) === "1" ? true : false
                                        }
                                        onChange={() => toggleUserCreate()}
                                      />
                                    </td>
                                    <td className="inst-list-td text-center">
                                      <input
                                        type="checkbox"
                                        checked={
                                          user.charAt(1) === "1" ? true : false
                                        }
                                        onChange={() => toggleUserView()}
                                      />
                                    </td>
                                    <td className="inst-list-td text-center">
                                      <input
                                        type="checkbox"
                                        checked={
                                          user.charAt(2) === "1" ? true : false
                                        }
                                        onChange={() => toggleUserUpdate()}
                                      />
                                    </td>
                                    <td className="inst-list-td text-center">
                                      <input
                                        type="checkbox"
                                        checked={
                                          user.charAt(3) === "1" ? true : false
                                        }
                                        onChange={() => toggleUserDelete()}
                                      />
                                    </td>
                                    <td className="inst-list-td text-center">
                                      <input
                                        type="checkbox"
                                        checked={user === "1111" ? true : false}
                                        onChange={() => toggleUserAll()}
                                      />
                                    </td>
                                  </tr>
                                  {/* INSTITUTE */}
                                  <tr>
                                    <td className="inst-list-td text-center">
                                      Institute
                                    </td>
                                    <td className="inst-list-td text-center">
                                      <input
                                        type="checkbox"
                                        checked={
                                          institute.charAt(0) === "1"
                                            ? true
                                            : false
                                        }
                                        onChange={() => toggleInstCreate()}
                                      />
                                    </td>
                                    <td className="inst-list-td text-center">
                                      <input
                                        type="checkbox"
                                        checked={
                                          institute.charAt(1) === "1"
                                            ? true
                                            : false
                                        }
                                        onChange={() => toggleInstView()}
                                      />
                                    </td>
                                    <td className="inst-list-td text-center">
                                      <input
                                        type="checkbox"
                                        checked={
                                          institute.charAt(2) === "1"
                                            ? true
                                            : false
                                        }
                                        onChange={() => toggleInstUpdate()}
                                      />
                                    </td>
                                    <td className="inst-list-td text-center">
                                      <input
                                        type="checkbox"
                                        checked={
                                          institute.charAt(3) === "1"
                                            ? true
                                            : false
                                        }
                                        onChange={() => toggleInstDelete()}
                                      />
                                    </td>
                                    <td className="inst-list-td text-center">
                                      <input
                                        type="checkbox"
                                        checked={
                                          institute === "1111" ? true : false
                                        }
                                        onChange={() => toggleInstAll()}
                                      />
                                    </td>
                                  </tr>
                                  {/* PACKAGE */}
                                  <tr>
                                    <td className="inst-list-td text-center">
                                      Package
                                    </td>
                                    <td className="inst-list-td text-center">
                                      <input
                                        type="checkbox"
                                        checked={
                                          pkg.charAt(0) === "1" ? true : false
                                        }
                                        onChange={() => togglePkgCreate()}
                                      />
                                    </td>
                                    <td className="inst-list-td text-center">
                                      <input
                                        type="checkbox"
                                        checked={
                                          pkg.charAt(1) === "1" ? true : false
                                        }
                                        onChange={() => togglePkgView()}
                                      />
                                    </td>
                                    <td className="inst-list-td text-center">
                                      <input
                                        type="checkbox"
                                        checked={
                                          pkg.charAt(2) === "1" ? true : false
                                        }
                                        onChange={() => togglePkgUpdate()}
                                      />
                                    </td>
                                    <td className="inst-list-td text-center">
                                      <input
                                        type="checkbox"
                                        checked={
                                          pkg.charAt(3) === "1" ? true : false
                                        }
                                        onChange={() => togglePkgDelete()}
                                      />
                                    </td>
                                    <td className="inst-list-td text-center">
                                      <input
                                        type="checkbox"
                                        checked={pkg === "1111" ? true : false}
                                        onChange={() => togglePkgAll()}
                                      />
                                    </td>
                                  </tr>
                                  {/* BILL */}
                                  <tr>
                                    <td className="inst-list-td text-center">
                                      Bill
                                    </td>
                                    <td className="inst-list-td text-center">
                                      <input
                                        type="checkbox"
                                        checked={
                                          bill.charAt(0) === "1" ? true : false
                                        }
                                        onChange={() => toggleBillCreate()}
                                      />
                                    </td>
                                    <td className="inst-list-td text-center">
                                      <input
                                        type="checkbox"
                                        checked={
                                          bill.charAt(1) === "1" ? true : false
                                        }
                                        onChange={() => toggleBillView()}
                                      />
                                    </td>
                                    <td className="inst-list-td text-center">
                                      <input
                                        type="checkbox"
                                        checked={
                                          bill.charAt(2) === "1" ? true : false
                                        }
                                        onChange={() => toggleBillUpdate()}
                                      />
                                    </td>
                                    <td className="inst-list-td text-center">
                                      <input
                                        type="checkbox"
                                        checked={
                                          bill.charAt(3) === "1" ? true : false
                                        }
                                        onChange={() => toggleBillDelete()}
                                      />
                                    </td>
                                    <td className="inst-list-td text-center">
                                      <input
                                        type="checkbox"
                                        checked={bill === "1111" ? true : false}
                                        onChange={() => toggleBillAll()}
                                      />
                                    </td>
                                  </tr>
                                  {/* PAYMENT */}
                                  <tr>
                                    <td className="inst-list-td text-center">
                                      Payment
                                    </td>
                                    <td className="inst-list-td text-center">
                                      <input
                                        type="checkbox"
                                        checked={
                                          payment.charAt(0) === "1"
                                            ? true
                                            : false
                                        }
                                        onChange={() => togglePaymentCreate()}
                                      />
                                    </td>
                                    <td className="inst-list-td text-center">
                                      <input
                                        type="checkbox"
                                        checked={
                                          payment.charAt(1) === "1"
                                            ? true
                                            : false
                                        }
                                        onChange={() => togglePaymentView()}
                                      />
                                    </td>
                                    <td className="inst-list-td text-center">
                                      <input
                                        type="checkbox"
                                        checked={
                                          payment.charAt(2) === "1"
                                            ? true
                                            : false
                                        }
                                        onChange={() => togglePaymentUpdate()}
                                      />
                                    </td>
                                    <td className="inst-list-td text-center">
                                      <input
                                        type="checkbox"
                                        checked={
                                          payment.charAt(3) === "1"
                                            ? true
                                            : false
                                        }
                                        onChange={() => togglePaymentDelete()}
                                      />
                                    </td>
                                    <td className="inst-list-td text-center">
                                      <input
                                        type="checkbox"
                                        checked={
                                          payment === "1111" ? true : false
                                        }
                                        onChange={() => togglePaymentAll()}
                                      />
                                    </td>
                                  </tr>
                                </tbody>
                              </table>
                              {permissionError && (
                                <ErrorMsg>
                                  * Minimum 1 Permission Required
                                </ErrorMsg>
                              )}
                            </div>
                            <button
                              type="submit"
                              className="btn btn-update mt-2 mb-2"
                            >
                              Update
                            </button>
                          </form>
                          {/* update role & permission form end */}
                          <LoadingIndicator />
                          {/* ----------------------------------------------------------------------- */}
                        </div>
                      </div>
                    </div>
                    {/* /.card */}
                  </div>
                  {/* Profile Edit end */}
                </div>
              </div>
              {/* Content End */}
            </div>
          </div>
        </div>
        {/* PAGE CONTENT END */}
      </div>
    </React.Fragment>
  );
}
