import { Link, useLocation } from "react-router-dom";
import React, { useState, useEffect } from "react";
import { useHistory } from "react-router-dom";
import { useForm } from "react-hook-form";
import axios from "axios";
import { trackPromise } from "react-promise-tracker";
import { LoadingIndicator } from "../Loading/LoadingIndicator";
import styled from "styled-components";
import { toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import "../../App.css";
import Moment from "moment";
import ViewEmail from "./ViewEmail";
export default function Draft() {
  const [data, setData] = useState([]);
  const [showViewEmail, setShowViewEmail] = useState(false);
  const [showDraft, setShowDraft] = useState(true);
  const [emailId, setEmailId] = useState("");
  const [updateApi, setUpdateApi] = useState(false);

  const history = useHistory();

  const emailAddress = localStorage.getItem("emailAddress");

  // FETCH USERTYPE LIST
  useEffect(() => {
    async function fetchData() {
      let accessToken = localStorage.getItem("token");
      // alert(accessToken);
      accessToken = accessToken.substring(1, accessToken.length - 1);
      var config = {
        method: "post",
        url: "/GetEmailForDraftBox",
        headers: {
          Authorization: `Bearer ${accessToken}`,
        },
        data: {
          email: emailAddress,
          //email: "turzoxpress3@gmail.com",
        },
      };

      const response = await trackPromise(axios(config));
      //console.log("response - " + JSON.stringify(response.data.data));
      setData(response.data.data);
      setUpdateApi(false);
    }
    fetchData();
  }, [updateApi]);

  return (
    <>
      <div className="row mb-4 float-right mr-2 mt-3">
        {showViewEmail ? (
          <span
            className="border ml-1 add-button shadow float-right"
            style={{ cursor: "pointer" }}
            title="Back to Draft"
            onClick={() => {
              setShowViewEmail(false);
              setShowDraft(true);
            }}
          >
            <i className="fas fa-arrow-left add-button-icon"></i>
          </span>
        ) : null}
      </div>
      <div className={`table-responsive ${showViewEmail ? null : "mt-5 pt-5"}`}>
        {showDraft ? (
          <table className="table table-hover institute-table">
            <thead>
              <tr className="inst-list-heading">
                <th className="inst-list-th text-center">
                  <input type="checkbox" />
                </th>
                <th className="inst-list-th text-center">From</th>
                <th className="inst-list-th text-center">To</th>
                <th className="inst-list-th text-center">Subject</th>
                <th className="inst-list-th text-center">Date</th>
                <th className="inst-list-th text-center">Action</th>
              </tr>
            </thead>
            <tbody>
              {data.map((row, index) => (
                <tr key={index}>
                  <td className="inst-list-td text-center">
                    <input type="checkbox" />
                  </td>
                  <td className="inst-list-td text-center">{emailAddress}</td>
                  <td className="inst-list-td text-center">
                    {" "}
                    {row["receiver"]}
                  </td>
                  <td className="inst-list-td text-center">{row["title"]}</td>
                  <td className="inst-list-td text-center">
                    {row["sending_date"]}
                  </td>
                  <td
                    className="inst-list-td text-center action"
                    title="View Details"
                    onClick={() => {
                      setShowViewEmail(true);
                      setShowDraft(false);
                      setEmailId(row["email_id"]);
                    }}
                  >
                    <i className="far fa-eye action-icon"></i>
                  </td>
                  {/* <td
                className="inst-list-td text-center action"
                title="Edit"
                onClick={() =>
                  history.push({
                    pathname: "/edit-user-type",
                    state: { id: row["id"] },
                  })
                }
              >
                <i className="far fa-trash-alt action-icon"></i>
              </td> */}
                </tr>
              ))}
            </tbody>
          </table>
        ) : null}
        {showViewEmail ? <ViewEmail email_id={emailId} /> : null}
      </div>
    </>
  );
}
