import React, { useState, useEffect } from "react";
import { useHistory } from "react-router-dom";
import { useLocation } from "react-router";
import { useForm } from "react-hook-form";
import axios from "axios";
import { trackPromise } from "react-promise-tracker";
import { LoadingIndicator } from "../Loading/LoadingIndicator";
import styled from "styled-components";
import { toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import "../../App.css";
const ErrorMsg = styled.p`
  color: red;
  font-size: 14px;
  font-family: "Source Sans Pro", sans-serif;
  font-weight: bold;
`;

export default function UpdateBillForm() {
  const { register, handleSubmit, errors } = useForm(); // initialise the hook

  const [month, setMonth] = useState("");
  const [year, setYear] = useState("");

  let history = useHistory();
  const location = useLocation();
  //console.log(location);

  const onSubmit = (data) => {
    //console.log(data);

    const userEmail = localStorage.getItem("emailAddress");
    data["generated_by"] = userEmail;
    data["id"] = location.state.id;

    let accessToken = localStorage.getItem("token");
    //alert(accessToken);
    accessToken = accessToken.substring(1, accessToken.length - 1);

    var config = {
      method: "post",
      url: "/UpdateInvoice",
      headers: {
        Authorization: `Bearer ${accessToken}`,
      },
      data: data,
    };

    trackPromise(
      axios(config).then(function (response) {
        // console.log(JSON.stringify(response.data));
        if (response.data.status === "ok") {
          toast(response.data.msg, {
            className: "toast-error",
          });
          history.push("/billing-list-view");
        } else {
          toast(response.data.msg, {
            className: "toast-error",
          });
        }
      })
    );
  };

  const [data, setData] = useState([]);

  //GET INVOICE -START
  useEffect(() => {
    async function fetchData() {
      let accessToken = localStorage.getItem("token");
      // alert(accessToken);
      accessToken = accessToken.substring(1, accessToken.length - 1);
      var config = {
        method: "post",
        url: "/ViewSingleInvoice",
        headers: {
          Authorization: `Bearer ${accessToken}`,
        },
        data: {
          id: location.state.id,
        },
      };

      const invoiceApiResp = await trackPromise(axios(config));
      // console.log(
      //   "invoiceApiResp - " + JSON.stringify(invoiceApiResp.data.msg)
      // );
      setData(invoiceApiResp.data.msg);
      setMonth(invoiceApiResp.data.msg.month);
      setYear(invoiceApiResp.data.msg.year);
    }
    fetchData();
  }, []);
  //GET INVOICE -END

  return (
    <div>
      {/* create form general info start */}
      <form className="mt-4" onSubmit={handleSubmit(onSubmit)}>
        <div className="form-group row">
          <div className="col-sm-12 col-md-6 col-lg-6 col-xl-6">
            <label>Institute ID</label>
            <br />
            <input
              type="text"
              className="form-control"
              name="institute_id"
              readOnly
              defaultValue={data.institute_id}
              ref={register({ required: true })}
            />
          </div>
        </div>

        <div className="form-group row">
          <div className="col-sm-12 col-md-6 col-lg-6 col-xl-6">
            <label>Institute Name</label>
            <br />
            <input
              type="text"
              className="form-control"
              name="institute_name"
              readOnly
              defaultValue={data.institute_name}
              ref={register({ required: true })}
            />
          </div>
        </div>

        <div className="form-group row">
          <div className="col-sm-12 col-md-6 col-lg-6 col-xl-6">
            <label>
              <span className="red-star">*</span>Billing Month
            </label>
            <br />
            <select
              className="form-control"
              name="month"
              value={month}
              onChange={(e) => setMonth(e.target.value)}
              ref={register({ required: true })}
            >
              <option value="January">January</option>
              <option value="February">February</option>
              <option value="March">March</option>
              <option value="April">April</option>
              <option value="May">May</option>
              <option value="June">June</option>
              <option value="July">July</option>
              <option value="August">August</option>
              <option value="September">September</option>
              <option value="October">October</option>
              <option value="November">November</option>
              <option value="December">December</option>
            </select>
            {errors.month && <ErrorMsg>* This Field is Required</ErrorMsg>}
          </div>
        </div>

        <div className="form-group row">
          <div className="col-sm-12 col-md-6 col-lg-6 col-xl-6">
            <label>
              <span className="red-star">*</span>Billing Year
            </label>
            <br />
            <select
              className="form-control"
              name="year"
              value={year}
              onChange={(e) => setYear(e.target.value)}
              ref={register({ required: true })}
            >
              <option value="2020">2020</option>
              <option value="2021">2021</option>
              <option value="2022">2022</option>
              <option value="2023">2023</option>
              <option value="2024">2024</option>
              <option value="2025">2025</option>
              <option value="2026">2026</option>
              <option value="2027">2027</option>
              <option value="2028">2028</option>
              <option value="2029">2029</option>
              <option value="2030">2030</option>
            </select>
            {errors.year && <ErrorMsg>* This Field is Required</ErrorMsg>}
          </div>
        </div>

        <div className="form-group row">
          <div className="col-sm-12 col-md-6 col-lg-6 col-xl-6">
            <label>
              <span className="red-star">*</span>Billing Amount
            </label>
            <br />
            <input
              type="text"
              className="form-control"
              name="amount"
              defaultValue={data.amount}
              ref={register({
                required: "* This Field is Required",
                pattern: {
                  value: /^[1-9]\d*$/,
                  message: "* Please Enter Number",
                },
              })}
            />
            {errors.amount && <ErrorMsg>{errors.amount.message}</ErrorMsg>}
          </div>
        </div>

        <button type="submit" className="btn btn-update">
          Update
        </button>
      </form>
      {/* create form general info end */}
      <LoadingIndicator />
    </div>
  );
}
